<template>
    <div>
        <div class="authOption">
            <p class="authTitle"><i class="el-icon-view"></i>&nbsp;阅读</p>
            <el-switch 
                v-model="Rnum" active-color="#606266" 
                inactive-color="#DCDFE6" 
                :disabled="level<5"
                size="small"
                class="switches"
            >
            </el-switch>
        </div>
        <div class="authOption">
            <p class="authTitle"><i class="el-icon-chat-round"></i>&nbsp;评论</p>
            <el-switch 
                v-model="Cnum" active-color="#606266" 
                inactive-color="#DCDFE6" 
                :disabled="level<5"
                size="small"
                class="switches"
            >
            </el-switch>
        </div>
        <div class="authOption">
            <p class="authTitle"><i class="el-icon-edit"></i>&nbsp;编辑</p>
            <el-switch 
                v-model="Enum" active-color="#606266" 
                inactive-color="#DCDFE6" 
                :disabled="level<5"
                size="small"
                class="switches"
            >
            </el-switch>
        </div>
        <!--确认修改-->
		<el-button plain style="margin-top:1.5ex;margin-bottom:1ex;width:50%" @click="submitForm()" v-if="level==5">确认修改</el-button>
    </div>
</template>

<script>
import QS from "qs";
const clickoutside = {
    update() {},
};
export default {
    props:{
      uid:Number,
      now_uid:Number,
      aid:Number,
      level:Number
    },
    name:'WeightList2',
    data(){
        return{
            Rnum: true,
            Cnum: true,
            Enum: true,
            Dnum: true,
            type: 'A',
        }
    },
    directives: {clickoutside},
    mounted: function() {
          if(this.level==5){
            this.type='B';
          }
          var param_data = {aid:this.aid};
          this.$axios.post("/api/getauthority2/", QS.stringify(param_data))
              .then(response => {
                  // post 成功，response.data 为返bai回的数据
                  this.Rnum=response.data.Rnum;
                  this.Cnum=response.data.Cnum;
                  this.Enum=response.data.Enum;
                  this.Dnum=response.data.Dnum;
                  console.log(response.data);
                  /*console.log(response.data.email)
                  console.log(response.data.mobile)
                  console.log(this.form.need_name)*/
              })
              .catch(error => {
                // 请求失败du
                console.log(error)
           })
    },
    methods: {
      change: function(callback){
        console.log(callback);
      },
      toProfile() {
        this.$router.push({ 
          path : "/user/profile",
          query: {
            UID : parseInt(this.form.uid),
            Judge:1
          }
        });
      },
      submitForm() {
        let param_data2={
            aid: this.aid,//文章ID
            Rnum: this.Rnum,//1-0读
            Cnum: this.Cnum,//1-0评论
            Enum: this.Enum,//1-0写
            Dnum: this.Dnum,//1-0删
        }
            this.$axios
            .post("/api/pushauthority2/",QS.stringify(param_data2))
            .then((response) =>{
                console.log(this.content);
                if(response.data.feedback){
                    console.log("4399999");
                    this.$message({
                    message:
                    "修改成功",
                    type: "success",
                    customClass: "c-msg",
                    duration: 1000
                });
                }
                else{
                    this.$message({
                    message: "修改失败",
                    type: "error",
                    customClass: "c-msg",
                    showClose: true,
                    duration: 1000
                }); 
                }
            })
            .catch(error => {
            console.log("!!!")
            this.isLoading = false;
            this.$message({
                message: "SubmitForm出了点状况",
                type: "error",
                customClass: "c-msg",
                duration: 0,
                showClose: true,
                duration: 1000
            });
            Promise.reject(error);
            });
        },
    }    
}
</script>
<style scoped>
.authTitle{
	width:45%;
	font-weight:bold;
	text-align:center;
	display:inline-block;
	font-size:120%;
	margin:auto auto auto auto;
}
.switches{
    width:45%;
    display:inline-block;
    text-align:center;
}
.authOption{
    margin-top:1ex;
}
</style>



