<template>
  <div>
    <div>
      <my-header :show-avatar="true" :show-search="true" @search="ToSearch"></my-header>
    </div>
    <div>
      <div id="left" style="display: inline-block;width: 10%;vertical-align: top;padding: 0px 30px 0px 0px;">
          <!-- <LeftSliderPro :team="myTeam" style="width: 100%" @changTeam="changeTeam"></LeftSliderPro>-->
        <LeftSliderPro :team="myTeam" style="width: 100%;" @changTeam="changeTeam"></LeftSliderPro>
      </div>
      <div id="main" style="display: inline-block;width: 70%;vertical-align: top;text-align: left">
        <div style="display: inline-block;width: 10%;cursor:pointer;" class="selected" id="lookUp" @click="Select('lookUp')">
          <p style="font-size: 15px;color: darkgray">最近浏览</p>
        </div>
        <div style="display: inline-block;width: 10%;cursor:pointer;" class="option" id="Edit" @click="Select('Edit')">
          <p style="font-size: 15px;color: darkgray">最近编辑</p>
        </div>
        <div style="display: inline-block;width: 10%;cursor:pointer;" class="option" id="Collect" @click="Select('Collect')">
          <p style="font-size: 15px;color: darkgray">收藏</p>
        </div>
        <div v-if="Documents11.length>0">
          <ShowFile :documents1="Documents11" :target11="cur_show" v-if="cur_show=='lookUp'"></ShowFile>
          <ShowFile  :documents1="Documents22" :target11="cur_show" v-else-if="cur_show=='Edit'"></ShowFile>
          <show-file :documents1="Documents33" :target11="cur_show" v-else></show-file>
        </div>
        </div>
      <div id="right" style="display: inline-block;width: 15%;vertical-align: center">
        <RightSlider :showUpload="false" :documents="Documents4" style="width: 100% ;margin-left: 10%"></RightSlider>
        <!--<div>
          <el-row>
            <el-col :span="24">
              <el-input v-model="search" @keyup.enter.native="ToSearch" style="width: 100%" >
                <el-button slot="append" icon="el-icon-search" id="search" @click="ToSearch" ></el-button>
              </el-input>
            </el-col>
          </el-row>
        </div> -->
      </div>
    </div>
  </div>
</template>

<script>
    import myHeader from "../../components/myHeader/myHeader";
    import LeftSider from "../../components/LeftSider/LeftSider";
    import ShowFile from "../../components/ShowFile/ShowFile";
    import RightSlider from "../../components/RightSlider/RightSlider";
    import LeftSliderPro from "../../components/LeftSliderPro/LeftSliderPro";
    import QS from "qs";
    export default {
        name: "craftTable",
        components:{
            myHeader,
            LeftSider,
            ShowFile,
            RightSlider,
            LeftSliderPro
        },
        created:function(){
          var uid = this.$store.state.userProfile.UID;
          var param_data = {UID :uid};
          this.$axios
          .post("/api/someInfo/",QS.stringify(param_data))
          .then(response => {
              this.myTeam = response.data.myTeam;
              this.Documents1 = response.data.Documents1;
              this.Documents2 = response.data.Documents2;
              this.Documents3 = response.data.Documents3;
              this.Documents11 = response.data.Documents1;
              this.Documents22 = response.data.Documents2;
              this.Documents33 = response.data.Documents3;
              this.Documents4 = response.data.Documents4;
              console.log("22222");
              console.log(this.Documents2);
          }).catch(error =>{
            alert(error);
          })
        },
      data(){
          return{
            search:"",
            cur_show:'lookUp',
            myTeam:[],
            Documents1:[
            ],
            Documents11:[
            ],
            Documents2:[
            ],
            Documents22:[],
            Documents3:[
            ],
            Documents33:[],
            Documents4:[]
          }
        },
        methods:{
          Select(target){
            if(this.cur_show=='Edit'){
              this.Documents22=this.Documents2;
            }
            else if(this.cur_show=='lookUp'){
              this.Documents11=this.Documents1;
            }
            else if(this.cur_show=='Collect'){
              this.Documents33=this.Documents3;
            }
            if(target=='lookUp'){
              this.cur_show=target;
            }
            else if(target=="Edit"){
              this.cur_show=target;
            }
            else if(target=="Collect"){
              this.cur_show=target;
            }
            this.search="";
            console.log(target);
            let selectedTag = document.getElementsByClassName("selected");
            if(selectedTag!=null){
              var t;
              for(t=0;t<selectedTag.length;t++){
                selectedTag[t].setAttribute("class","option");
              }
            }
            /*把当前变成selected */
            document.getElementById(target).setAttribute("class","selected");
          },
          changeTeam(params){
              this.myTeam.push(params);
          },
          ToSearch(target){
            console.log("王勇".toLowerCase());
            let newArr = [];
            let search1 = target.toLowerCase();
            console.log(search1);
            var i;
            if(this.cur_show=='lookUp'){
                for(i=0;i<this.Documents1.length;i++){
                    if(this.Documents1[i].documentName.length<search1.length){
                        let newArr1 = this.Documents1[i].documentName.toLowerCase();
                        console.log(newArr1);
                        if(search1.indexOf(newArr1)!=-1){
                            newArr.push(this.Documents1[i]);
                        }
                    }
                    else{
                      let newArr1 = this.Documents1[i].documentName.toLowerCase();
                      console.log(newArr1);
                      if(newArr1.indexOf(search1)!=-1){

                          newArr.push(this.Documents1[i]);
                      }
                    }
                }
                console.log("查询数组");
                this.Documents11=newArr;
            }
            else if(this.cur_show=='Edit'){
              for(i=0;i<this.Documents2.length;i++){
                if(this.Documents2[i].documentName.length<this.search.length){
                  let newArr1 = this.Documents2[i].documentName.toLowerCase();
                  if(search1.indexOf(newArr1)!=-1){
                    console.log(newArr1);
                    newArr.push(this.Documents2[i]);
                  }
                }
                else{
                  let newArr1 = this.Documents2[i].documentName.toLowerCase();
                  console.log(newArr1);
                  if(newArr1.indexOf(search1)!=-1){
                    newArr.push(this.Documents2[i]);
                  }
                }
              }
              console.log("查询数组");
              this.Documents22=newArr;
            }
            else if(this.cur_show=='Collect'){
              for(i=0;i<this.Documents3.length;i++){
                if(this.Documents3[i].documentName.length<this.search.length){
                  let newArr1 = this.Documents3[i].documentName.toLowerCase();
                  if(search1.indexOf(newArr1)!=-1){
                    console.log(newArr1);
                    newArr.push(this.Documents3[i]);
                  }
                }
                else{
                  let newArr1 = this.Documents3[i].documentName.toLowerCase();
                  console.log(newArr1);
                  if(newArr1.indexOf(search1)!=-1){
                    newArr.push(this.Documents3[i]);
                  }
                }
              }
              console.log("查询数组");
              this.Documents33=newArr;
            }
          }
        },
    }
</script>


<style scoped>
  .option{
    font-size:150%;
    font-weight: normal;
  }
  .option:hover{
    font-size:100%;
    font-weight: bold;
    border-bottom:2px solid darkgray;
  }
  .option:active{
    font-size: 100%;
    font-weight: bold;
    border-bottom: 2px solid black;
  }
  .selected{
    font-size: 50%;
    font-weight: bold;
    border-bottom: 2px solid gray;
  }
</style>
<style scoped>
  .option{
    font-size:150%;
    font-weight: normal;
  }
  .option:hover{
    font-size:100%;
    font-weight: bold;
    border-bottom:2px solid darkgray;
  }
  .option:active{
    font-size: 100%;
    font-weight: bold;
    border-bottom: 2px solid black;
  }
  .selected{
    font-size: 50%;
    font-weight: bold;
    border-bottom: 2px solid gray;
  }
</style>




