<template>
    <div id="main">
        <myHeader :showAvatar="true" :showSearch="false"></myHeader>
        <div style="vertical-align:top">
            <!--左栏-->
            <div style="display:inline-block;width:10%;vertical-align:top">
              <LeftSliderPro :team="teams" style="width: 100%;"></LeftSliderPro>
            </div>
            <div style="display:inline-block;width:70%;vertical-align:top">
                <!--选项卡，区分编辑区和评论区-->
                <div style="width:75%;margin-left:2.5%;text-align:center">
                    <el-tabs
                        v-if="this.$route.params.AID!=-1"
                        v-model="activeName"
                        style="display:inline-block;width:30%"
                        :stretch="true"
                    >
                        <el-tab-pane label="文本编辑" name="editor" style="text-align:center;"></el-tab-pane>
                        <el-tab-pane label="讨论交流" name="comment" style="text-align:center"></el-tab-pane>
                    </el-tabs>
                </div>
                <!--右栏-->
                <!--编辑器-->
                <editor v-show="activeName=='editor'" style="width:100%" :Editable="CanEdit" :MyLevel="UserLevel" :isTagVisible="true"></editor>
                <!--评论区-->
                <comment
                    :AID="this.$route.params.AID"
                    :md5="0"
                    :myuid="this.$store.state.userProfile.UID"
                    :commentlevel="CanComment?1:4"
                    :level="CanComment?4:1"
                    v-show="activeName=='comment'"
                    style="width:75%;margin-left:2.5%"
                >
                </comment>
            </div>
        </div>
    </div>
</template>
<script>
import leftSider from "@/components/LeftSider/LeftSider.vue"
import editor from "@/components/editor/editor.vue"
import myHeader from "@/components/myHeader/myHeader.vue"
import comment from "@/components/Comment/Comment.vue"
import LeftSliderPro from "../../components/LeftSliderPro/LeftSliderPro";
export default {
    name:"myDocEditor",
    components:{
        leftSider,
        editor,
        myHeader,
        comment,
        LeftSliderPro
    },
    data(){
        return{
            activeName:"editor",
            teams:[],//团队列表
            CanEdit:true,
            CanComment:true,
            CanRead:true,
            UserLevel:0
        }
    },
    /*刚刚创建，应该想办法向后台获取团队信息 */
    /*TODO 向后端获取当前文章所属的团队 */
    /*TODO 向后端了解这篇文章是不是模板 */
    created(){
        this.getTeamInfo();
        if(this.$route.params.AID!=-1)this.getUserAuth();
    },
    methods:{
        /*从后端获取团队列表 */
        getTeamInfo(){
            let form = new FormData();
            form.append("UID", this.$store.state.userProfile.UID);
            this.$axios.post("/api/teamList/",form)
            .then(response=>{
                if(response.data.Status){//检索成功
                    var list = response.data.teams;
                    for(var i in list){
                        console.log(i);
                        this.teams.push({
                            TID:list[i].TID,
                            teamName:list[i].teamName
                        });
                    }
                }
                else{
                    this.$message({
                        message:"获取信息失败",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"服务器好像出了点问题",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
            })
        },
        /*获取相关的用户权限信息 */
        getUserAuth(){
            let form = new FormData();
            form.append("UID", this.$route.params.UID);
            form.append("AID", this.$route.params.AID);
            form.append("TID", this.$route.params.TID);
            this.$axios.post("/api/getUserAuthority/",form)
            .then(response=>{
                this.CanEdit = response.data.CanEdit;
                this.CanComment = response.data.CanComment;
                this.CanRead = response.data.CanRead;
                if(!this.CanRead){//不可阅读，返回工作台
                    this.$message({
                        message:"对不起，您的权限不足以查看这篇文章",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
                    this.$router.push({
                        path:"/craftTable",
                        params:{
                            UID:this.$route.params.UID
                        }
                    })
                }
                this.UserLevel = response.data.UserLevel;
                console.log("用户权限等级"+this.UserLevel);
            })
            .catch(error=>{
                this.$message({
                    message:"服务器好像有点问题",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
            })
        }
    }
}
</script>
<style scoped>

</style>



