<template>
    <div style="border:1px solid #e0e0e0;width:30%;border-radius:5px;background:#ffffff">
        <h1>欢迎注册</h1>
        <hr style="width:90%">
        <form
            id = "outer"
            style="text-align:center;padding-left:10%;padding-right:10%;padding-top:5px;padding-bottom:5px"
            novalidate="novalidate"
        >
            <!--输入用户名-->
            <div style="text-align:left;width:100%;padding:1ex">
                <label for="Username" style="text-align:left;font-size:120%">用户名<span style="color:red">*</span></label><br>
                <el-input 
                    type="input" 
                    name="Username" 
                    id="Username" 
                    v-model="Username" 
                    placeHolder="请输入用户名" 
                    style="width:100%;font-size:120%;margin-top:0.5ex"
                    @blur="validateUsername()"
                />
                <br>
            </div>
            <!--输入邮箱，以及发送验证码的按钮-->
            <div style="text-align:left;width:100%;padding:1ex">
                <label for="mail" style="margin-top:1ex;text-align:left;font-size:120%">邮箱<span style="color:red">*</span></label><br>
                <el-input 
                    type="email" 
                    name="Email" 
                    placeholder="请输入您的邮箱" 
                    id="mail" 
                    v-model="email" 
                    style="width:75%;font-size:120%;margin-top:0.5ex"
                />
                <div style="display:inline-block;text-align:right;width:24%">
                    <el-button type="primary" plain class="button" size="medium" style="width:100%;margin-right:0px;font-size:120%" @click="sendVerify()">
                        验证
                    </el-button>
                </div>
                <br>
            </div>
            <!--邮箱验证码-->
            <div style="text-align:left;width:100%;padding:1ex" v-if="emailSent">
                <label for="captcha" style="text-align:left;font-size:120%">邮箱验证码<span style="color:red">*</span></label><br>
                <el-input 
                    type="input" 
                    name="Captcha" 
                    id="captcha" 
                    v-model="captcha" 
                    style="width:100%;font-size:120%;margin-top:0.5ex"
                /><br>
            </div>
            <!--输入密码-->
            <div style="text-align:left;width:100%;padding:1ex;">
                <label for="password" style="text-align:left;font-size:120%">密码<span style="color:red">*</span></label><br>
                <el-input 
                    type="password" 
                    name="Password" 
                    placeholder="请输入密码" 
                    id="password" 
                    v-model="password" 
                    style="width:100%;font-size:120%;margin-top:0.5ex"
                    show-password
                />
                <br>
            </div>
            <!--确认密码-->
            <div style="text-align:left;width:100%;padding:1ex">
                <label for="password-again" style="text-align:left;font-size:120%">再次输入密码<span style="color:red">*</span></label><br>
                <el-input 
                    type="password" 
                    placeholder="请再次确认密码" 
                    id="password-again" 
                    v-model="again" 
                    style="width:100%;font-size:120%;margin-top:0.5ex"

                /><br>
            </div>
            <el-button plain type="primary" class="button" value="注册" style="margin-top:1ex;width:30%;margin-bottom:1ex" @click="FormSubmit()">注册</el-button>
        </form>
    </div>
</template>
<script>
export default {
    data(){
        return{
            Username:"",
            email:"",
            password:"",
            again:"",
            captcha:"",
            emailSent:false,
            UsernameOK:false,
            emailOK:false,
            passwordOK:false
        }
    },
    methods:{
        /*提交整个表单 */
        async FormSubmit(){
            console.log("我要提交了");
            let form = new FormData();
            var isPasswordOk = await this.validatePassword();
            if(!isPasswordOk){
                console.log("密码挂了");
                return;
            }
            var isEmailOk = await this.validateEmailAddr();
            if(!isEmailOk){
                console.log("邮箱挂了");
                return;
            }
            var isUsernameOk = await this.validateUsername();
            if(!isUsernameOk){
                console.log("用户名挂了");
                return;
            }
            if(this.captcha==""){
                this.$message({
                    message:"请填写验证码",
                    type:"error",
                    duration:1000,
                    showClose:true
                });
                console.log("验证码挂了");
                return;
            }
            form.append("Captcha",this.captcha);
            form.append("Username",this.Username);
            form.append("Password",this.$md5(this.password));
            form.append("Email",this.email);
            this.$axios.post("/api/register/",form)
            .then(response=>{
                if(response.data.Status){
                    this.$message({
                        message:"注册成功",
                        type:"success",
                        duration:1000,
                        showClose:true
                    })
                    this.$router.push("/");
                }
                else{
                    this.$message({
                        message:"注册失败",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
                }
            }).catch(error=>{
                this.$message({
                        message:"服务器好像出了点问题",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
            })
        },
        /*提交邮箱 */
        async sendVerify(){
            var isEmailOk = await this.validateEmailAddr();
            if(!isEmailOk){

            }
            else{
                this.emailSent=true;
                console.log("正在提交邮箱")
                let form = new FormData();
                form.append("Email",this.email);
                this.$axios.post("/api/captcha/",form)
                .then(response=>{
                    if(response.data.Status){
                        this.$message({
                            message:"验证码发送成功，请检查您的邮箱，验证码10分钟内有效",
                            type:"success",
                            duration:1000,
                            showClose:true
                        })
                    }
                    else{
                        this.$message({
                            message:"验证码发送失败，请检查您填写的邮箱地址",
                            type:"error",
                            duration:1000,
                            showClose:true
                        })
                    }
                }).catch(error=>{
                    this.$message({
                        message:"服务器可能有点问题",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
                })
            }
        },
        /*判断邮箱地址是否合法 */
        judgeEmailAddr(email){
            var emreg=/^\w{3,}(\.\w+)*@[A-z0-9]+(\.[A-z]{2,5}){1,2}$/;
            return emreg.test(email);
        },
        /*判断邮箱是合法（格式，重复性） */
        async validateEmailAddr(){
            if(!this.judgeEmailAddr(this.email)){//判断格式
                console.log("哈哈哈");
                this.$message({
                    message:"邮箱格式有误",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
                return false;
            }
            else{
                let form = new FormData();
                form.append("Email",this.email);
                var result;
                await this.$axios.post("/api/checkEmail/",form)
                .then(response=>{
                    if(response.data.Used){
                        this.$message({
                            message:"这个邮箱已经被使用过了",
                            type:"error",
                            duration:1000,
                            showClose:true
                        })
                        result = false;
                    }
                    else result = true;
                })
                .catch(error=>{
                    this.$message({
                        message:"服务器好像有点问题",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
                    result = false;
                })
                return result;
            }
        },
        /*判断用户名是否合法 */
        async validateUsername(){
            this.UsernameOK=false;
            if(this.Username==""){
                this.$message({
                    message:"用户名不得为空",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
                return false;
            }
            else{
                let form = new FormData();
                form.append("Username",this.Username);
                var result;
                await this.$axios.post("/api/checkUsername/",form)
                .then(response=>{
                    if(response.data.Used){
                        console.log("用户名检测结果:"+response.data.Used);
                        this.$message({
                            message:"很遗憾，这个名字已经被用过了",
                            type:"error",
                            duration:1000,
                            showClose:true
                        })
                        console.log("第1句话被执行");
                        result = false;
                    }
                    else{
                        this.UsernameOK=true;
                        console.log("用户名检测结果:"+response.data.Used);
                        console.log("第2句话被执行");
                        result = true;
                    }
                })
                .catch(error=>{
                    console.log("第3句话被执行");
                    result = false;
                })
                console.log("结果是："+result);
                return result;
            }
        },
        /*判断密码是否合法 */
        async validatePassword(){
            console.log("开始验证密码");
            var i;
            if(this.password.length!=this.again.length){
                this.$message({
                    message:"两次输入的密码不一样",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
                return false;
            }
            var hasCapital = false;
            var hasNumber = false;
            var hasNormal = false;
            for(i=0;i<this.password.length;i++){
                if(this.password.charAt(i)!=this.again.charAt(i)){
                    this.$message({
                        message:"两次输入的密码不一样",
                        type:"error",
                        duration:1000,
                        showClose:true
                    });
                    return false;
                }
                if(this.password.charAt(i)>='a'&&this.password.charAt(i)<='z'){
                    console.log("有小写字母");
                    hasNormal=true;
                }
                if(this.password.charAt(i)>='0'&&this.password.charAt(i)<='9'){
                    console.log("有数字");
                    hasNumber=true;
                }
                if(this.password.charAt(i)>='A'&&this.password.charAt(i)<='Z'){
                    console.log("有大写字母");
                    hasCapital=true;
                }
            }
            if(!(hasNormal&&hasNumber&&hasCapital)){
                if(!hasNumber){
                    this.$message({
                        message:"密码太简单，应含有至少一个数字",
                        type:"error",
                        duration:1000,
                        showClose:true
                    });
                }
                else if(!hasCapital){
                    this.$message({
                        message:"密码太简单，应含有至少一个大写字母",
                        type:"error",
                        duration:1000,
                        showClose:true
                    });
                }
                else{
                    this.$message({
                        message:"密码太简单，应含有至少一个小写字母",
                        type:"error",
                        duration:1000,
                        showClose:true
                    });
                }
                return false;
            }
            return true;
        }
    }
}
</script>
<style scoped>

</style>




