<!--首先文件是根据最后一次编辑时间分类的，所以我们要知道文件的编辑时间，然后要显示文档的名字,文档创建者，文档最后一次编辑时间-->
<template>
  <div>
    <div v-for="(item,index) in DocumentsSortedByDate">
      <p class="date">{{ item.lastEditDate }}</p>
      <el-row :gutter="6">
        <el-col :span="8" v-for="(item1,index1) in item.otherInformation" >
          <el-card :body-style="{ padding:'0' }" shadow="hover" style="width:100%;height:100%;text-align: left;vertical-align: middle;margin-bottom: 5%;min-height: 80px" @mouseover.native="mouseOver(index,index1)"  @mouseleave.native="mouseLeave(index,index1)">
            <i class="el-icon-document" style="font-size: 300%;vertical-align: middle;display: inline-block"></i>
            <div style="display: inline-block;margin-left: 0px;text-align: left;vertical-align: middle;cursor: pointer" @click="go(item1)">
              <div>
                <span style="font-size: 80%">文档名:</span>
                <span style="font-size: 80%;color: black;">{{ item1.documentName }}</span>
              </div>
              <div class="bottom clearfix">
                <span style="font-size: 80%;color: darkgray">最后一次修改时间:</span>
                <time class="time" style="font-size: 80%;"> {{ item1.documentLastEditDate1 }} </time>
              </div>
              <div class="unsure">
                <span style="font-size: 80%;color: darkgray">文档拥有者:</span>
                <span style="font-size: 80%;color: darkgray">{{ item1.documentOwner }}</span>
              </div>
            </div>
            <el-popover :ref="'popover'+index+index1" style="padding: 0px 0px 0px 0px;" trigger="click">
              <div>
                <div class="option" style="margin-top:0px;font-size:125%;cursor:pointer" @click="DeleteFile(item1)" >
                  <div style="padding-top:4px;padding-bottom:4px">
                    <i class="el-icon-delete-solid" style="margin-top:2px;margin-bottom:2px"></i>
                    <span style="text-align:center">删除</span>
                  </div>
                </div>
                <div class="option" style="margin-top:0px;font-size:125%;cursor:pointer" @click="UnCollectFile(item1)" v-if="target11=='Collect'">
                  <div style="padding-top:4px;padding-bottom:4px" >
                    <i class="el-icon-star-on" style="margin-top:2px;margin-bottom:2px"></i>
                    <span style="text-align:center">取消收藏</span>
                  </div>
                </div>
                <div class="option" style="margin-top:0px;font-size:125%;cursor:pointer" v-else>
                  <div style="padding-top:4px;padding-bottom:4px" v-if="item1.isCollect==false" @click="CollectFile(item1)">
                    <i class="el-icon-star-off" style="margin-top:2px;margin-bottom:2px"></i>
                    <span style="text-align:center">收藏</span>
                  </div>
                  <div style="padding-top:4px;padding-bottom:4px" v-else @click="UnCollectFile(item1)">
                    <i class="el-icon-star-on" style="margin-top:2px;margin-bottom:2px"></i>
                    <span style="text-align:center">取消收藏</span>
                  </div>
                </div>
              </div>
            </el-popover>
            <div style="display: inline-block;vertical-align: top;float: right" v-show="isVisible[index][index1]">

              <p style="display:inline-block;height:100%;vertical-align:middle;margin:auto;width:0%"></p>
              <i class="el-icon-s-tools" v-popover="'popover'+index+index1"></i>
            </div>
            <el-row style="margin-left: 11%">
              <div v-for="tag in item1.Tags">
                <el-col :span="4" :offset="1" v-if="tag.type=='danger'" style="display: inline-block">
                  <div class="redCircle" style="display: inline-block"></div>
                  <div style="display: inline-block;font-size: 12px;color: darkgray">{{ tag.name }}</div>
                </el-col>
                <el-col :span="4" :offset="1" v-else-if="tag.type=='warning'" style="display: inline-block">
                  <div class="yellowCircle" style="display: inline-block"></div>
                  <div style="display: inline-block;font-size: 12px;color: darkgray">{{ tag.name }}</div>
                </el-col>
                <el-col :span="4" :offset="1" v-else-if="tag.type=='success'" style="display: inline-block">
                  <div class="greenCircle" style="display: inline-block"></div>
                  <div style="display: inline-block;font-size: 12px;color: darkgray">{{ tag.name }}</div>
                </el-col>
                <el-col :span="4" :offset="1" v-else-if="tag.type=='info'" style="display: inline-block">
                  <div class="blackCircle" style="display: inline-block"></div>
                  <div style="display: inline-block;font-size: 12px;color: darkgray">{{ tag.name }}</div>
                </el-col>
                <el-col :span="4" :offset="1" v-else style="display: inline-block">
                  <div class="blueCircle" style="display: inline-block"></div>
                  <div style="display: inline-block;font-size: 12px;color: darkgray">{{ tag.name }}</div>
                </el-col>
              </div>
            </el-row>
          </el-card>
        </el-col>
      </el-row>
    </div>
  </div>

</template>

<script>
  export default {
    name: "ShowFile",
    props:['documents1','target11'],
    data(){
      return{
        isVisible:[],
        inputVisible:false,
        inputValue:""
      }
    },
    mounted:function(){
      let i;
      let j;
      this.isVisible= new Array();
      for(i =0 ;i<this.DocumentsSortedByDate.length;i++){
        this.isVisible[i]=new Array();
      }
      for(i =0 ;i<this.DocumentsSortedByDate.length;i++){
        for(j=0;j<this.DocumentsSortedByDate[i].otherInformation.length;j++){
          this.$set(this.isVisible[i],j,false);
        }
      }
    },
    //假设我们接受的数组中包含lastEditDate,Name;
    computed:{
      DocumentsSortedByDate(){
        console.log(this.documents1);
        let newArr = [];
        this.documents1.forEach((item,i) =>{
          let index=-1;
          let flag=newArr.some((item1,j)=>{
            //二者日期相同
            if(this.setDate(item.lastEditDate) === item1.lastEditDate){
              index=j;
              return true;
            }
          })
          //如果不存在
          if(!flag){
            newArr.push({
              lastEditDate:this.setDate(item.lastEditDate),
              otherInformation:
                [
                  {
                    documentName:item.documentName,
                    documentOwner:item.documentOwner,
                    documentLastEditDate1:item.lastEditDate,
                    AID:item.AID,
                    TID:item.TID,
                    isCollect:item.isCollect,
                    Tags:item.Tags
                  }
                ]
            })
          }
          else{
            newArr[index].otherInformation.push({
              documentName:item.documentName,
              documentOwner:item.documentOwner,
              documentLastEditDate1:item.lastEditDate,
              AID:item.AID,
              TID:item.TID,
              isCollect:item.isCollect,
              Tags:item.Tags
            })
          }
        })
        var i;
        for(i=0;i<newArr.length;i++){
          newArr[i].otherInformation.sort(this.sortBy1);
        }
        console.log(newArr);
        return newArr.sort(this.sortBy);
      }
    },
    methods:{
      setDate(date){
        let newDate = new Date(date);
        let year=newDate.getFullYear().toString();
        let month=(newDate.getMonth()+1).toString();
        let day=newDate.getDate().toString();
        return year+"年"+month+"月"+day+"日";
      },
      sortBy(a,b){
        return b.lastEditDate.localeCompare(a.lastEditDate);
      },
      sortBy1(a,b){
        return b.documentLastEditDate1.localeCompare(a.documentLastEditDate1);
      },
      mouseOver(index,index1){
        this.$set(this.isVisible[index],index1,true);
        this.$forceUpdate();
        console.log(index);
        console.log(index1);
        console.log(typeof this.isVisible[index][index1]);
        console.log(this.isVisible[index][index1]==true);
      },
      mouseLeave(index,index1){
        this.$set(this.isVisible[index],index1,false);
        this.$forceUpdate();
        console.log(index);
        console.log(index1);
        console.log(typeof this.isVisible[index][index1]);
        console.log(this.isVisible[index][index1]==true);
      },
      DeleteFile(item1){
        let newForm = new FormData();
        newForm.append("UID",this.$store.state.userProfile.UID);
        newForm.append("AID",item1.AID);
        newForm.append("TID",item1.TID);
        this.$axios
          .post("/api/deleteFile/",newForm)
          .then(response =>{
            if(response.data.status){
              var i;
              for(i=0;i<this.documents1.length;i++){
                if(this.documents1[i].AID == item1.AID){
                  this.documents1.splice(i,1);
                }
              }
              this.$message({
                message:"删除成功",
                type:"success",
                customClass:"c-msg"
              })
              this.$router.go(0);
            }
            else{
              this.$message({
                message:"删除失败",
                type:"error",
                customClass:"c-msg"
              })
            }
          }).catch(error =>{
          this.$message({
            message:"该页面出现了一些情况",
            type:"error",
            customClass:"c-msg",
            duration:0,
            showClose:true
          });
          Promise.reject(error);
        })
      },
      CollectFile(item1){
        let newForm = new FormData();
        newForm.append("UID",this.$store.state.userProfile.UID);
        newForm.append("AID",item1.AID);
        this.$axios
          .post("/api/collectFile/",newForm)
          .then(response =>{
            if(response.data.status){
              this.$message({
                message:"收藏成功",
                type:"success",
                customClass:"c-msg"
              });
              this.$router.go(0);
            }
            else{
              this.$message({
                message:"收藏失败",
                type:"error",
                customClass:"c-msg"
              })
            }
          }).catch(error =>{
          this.$message({
            message:"该页面出现了一些情况",
            type:"error",
            customClass:"c-msg",
            duration:0,
            showClose:true
          });
          Promise.reject(error);
        })
      },
      UnCollectFile(item1){
        let newForm = new FormData();
        newForm.append("UID",this.$store.state.userProfile.UID);
        newForm.append("AID",item1.AID);
        this.$axios
          .post("/api/unCollectFile/",newForm)
          .then(response =>{
            if(response.data.status){
              var i;
              for(i=0;i<this.documents1.length;i++){
                if(this.documents1[i].AID == item1.AID){
                  this.documents1.splice(i,1);
                }
              };
              this.$message({
                message:"取消收藏成功",
                type:"success",
                customClass:"c-msg"
              });
              this.$router.go(0);
            }
            else{
              this.$message({
                message:"取消收藏失败",
                type:"error",
                customClass:"c-msg"
              })
            }
          }).catch(error =>{
          this.$message({
            message:"该页面出现了一些情况",
            type:"error",
            customClass:"c-msg",
            duration:0,
            showClose:true
          });
          Promise.reject(error);
        })
      },
      go(item1){
          let newForm = new FormData();
          newForm.append("UID",this.$store.state.userProfile.UID);
          newForm.append("AID",item1.AID);
          newForm.append("TID",item1.TID);
          this.$axios
              .post("/api/isShared/",newForm)
              .then(response =>{
                    console.log(response.data.status);
                    if(response.data.status){
                        console.log("这个执行了吗")
                        console.log(item1.AID);
                        console.log(typeof(item1.AID));
                        console.log(this.$md5(item1.AID.toString()));
                        this.$router.push("/sharepage/"+this.$store.state.userProfile.UID+"/"+response.data.md5);
                    }
                    else{
                        if(item1.TID==""){
                            this.$router.push("/Edit/"+this.$store.state.userProfile.UID+"/"+item1.AID+"/"+"-1");
                      }
                      else{
                            this.$router.push("/Edit/"+this.$store.state.userProfile.UID+"/"+item1.AID+"/-1/"+item1.TID);
                      }
                    }
              }).catch(error=>{
              this.$message({
                  message:"跳转出错",
                  type:"error",
                  customClass:"c-msg",
                  duration:0,
                  showClose:true
            });
            Promise.reject(error);
          })
      }
    },
    watch:{
      documents1:{
        handler(newValue,oldValue){
          console.log(newValue);
          this.documents1=newValue;
        }
      }
    }
  }
</script>

<style scoped>
  .date{
    font-size: 12px ;
    color: darkgray;
    text-align: left;
  }
  .el-card{
    border: 0;
  }
  .bottom {
    float: left;
  }
  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }
  .time{
    color: darkgray;
  }
  .option:hover{
    background:#f0f0f0;
    font-weight:bold
  }
  .option:active{
    background:#e8e8e8;
    font-weight:bold;
  }
  .button-new-tag {
    margin-left: 10px;
    height: 100%;
    padding-top: 0;
    padding-bottom: 0;
  }
  .input-new-tag {
    height: 100%;
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  }
  .redCircle{
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background-color: #F56C6C;
  }
  .yellowCircle{
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #E6A23C;
  }
  .blueCircle{
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #409EFF;
  }
  .greenCircle{
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #67C23A;
  }
  .blackCircle{
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #909399;
  }
</style>



