<template>
    <div>
        <myHeader :showAvatar="true" :showSearch="false" @search="changeContent"></myHeader>
        <memberList style="width:15%;min-width:300px" :myLevel=5 :tid=6></memberList>
        搜索框内容：{{content}}
    </div>
</template>
<script>
import myHeader from "@/components/myHeader/myHeader.vue"
import memberList from "@/components/memberList/memberList.vue"
export default {
    components:{
        myHeader,
        memberList
    },
    data(){
        return{
            content:"",
            testData:[
                {
                    AvatarUrl:"",
                    Username:"dhz",
                    Level:3
                },
                {
                    AvatarUrl:"",
                    Username:"tz",
                    Level:2
                },
                {
                    AvatarUrl:"",
                    Username:"asd",
                    Level:1
                }
            ]
        }
    },
    methods:{
        changeContent(target){
            console.log("search事件接收成功"+target)
            this.content=target;
        }
    }
}
</script>
<style scoped>

</style>


