<template>
    <!--总体-->
    <div>
        <!--中间的编辑器部分-->
        <div style="display:inline-block;width:75%;vertical-align:top">
            <!--输入题目-->
            <input
                type="text"
                style="width:95%;
                    border:0px;
                    border-bottom:1px solid #aaaaaa;
                    font-size:200%;
                    margin-top:5px;
                    text-align:center;
                    padding:5px;
                    "
                placeholder="请输入文档题目"
                v-model="articleInfo.title"
                :disabled="!this.Editable"
            />
            <!--输入内容-->
            <mavon-editor
                style="width:99%;margin-top:10px;box-shadow:0px 0px 0px #fff;height:90ex"
                v-model="articleInfo.content"
                :subfield="this.Editable"
                :defaultOpen="'preview'"
                ref="editor"
                @imgAdd="$imgAdd"
                :toolbarsFlag="this.Editable"
            />
        </div>
        <!--右边的按钮区-->
        <div style="display:inline-block;text-align:center;width:20%;vertical-align:top">
            <div style="width:100%;margin-top:2ex">
                <!--保存文档选项-->
                <el-button plain style="width:80%" @click="saveAsMine('doc')" :disabled="!this.Editable">
                    保存
                </el-button>
                <br>
                <!--保存为模板-->
                <el-button plain style="width:80%;margin-top:1ex" @click="saveAsMine('model')" :disabled="!this.Editable">
                    另存为新模板
                </el-button>
                <br>
            </div>
            <!--标签区-->
            <div style="border:1px solid #e8e8e8;background:white;margin-top:2ex;border-radius:1ex;min-height:4em;text-align:left;padding:0.5ex" v-if="isTagVisible">
                <div style="float:left">
                    <i class="el-icon-price-tag icon-transform" style="margin-top:0.5em"></i>
                    <span style="margin-right:0.5em;margin-top:0.5em">标签</span>          
                    <el-tag v-for="tag in Tags" closable :disable-transitions="false" :key="tag.name" :type="tag.type" size="small" @close="handleClose(tag)">
                        {{tag.name}}
                    </el-tag>
                    <span v-popover:pop><i class="el-icon-circle-plus" style="margin-top:0.5em"></i></span>
                </div>
                <!--<el-button class="button-new-tag" size="small" v-popover:pop style="float:left">+New Tag</el-button>-->
            </div>
            <el-popover popper-class="tagBox" ref="pop" style="padding:0px 0px 0px 0px" trigger="click" placement="bottom">     
                <div>
                    <el-input 
                        class="input-new-tag"  
                        v-model="inputValue.name" 
                        ref="saveTagInput" 
                        size="small"
                        @keyup.enter.native="handleInputConfirm" 
                        style="width:90%;margin-top:1ex"
                        :placeholder="'在此处新建标签'"
                    >
                    </el-input>
                    <!--新建标签的地方-->
                    <ul class="myList">
                        <li class="myListItem"><div class="blueCircle" @click="changeType(1)"><i class="el-icon-check" v-if="checkVisible==1"></i></div></li>
                        <li class="myListItem"><div class="greenCircle" @click="changeType(2)"><i class="el-icon-check" v-if="checkVisible==2"></i></div></li>
                        <li class="myListItem"><div class="yellowCircle" @click="changeType(3)"><i class="el-icon-check" v-if="checkVisible==3"></i></div></li>
                        <li class="myListItem"><div class="redCircle" @click="changeType(4)"><i class="el-icon-check" v-if="checkVisible==4"></i></div></li>
                        <li class="myListItem"><div class="greyCircle" @click="changeType(5)"><i class="el-icon-check" v-if="checkVisible==5"></i></div></li>
                    </ul>
                    <!--当前的标签-->
                    <div v-if="allTags.length!=0" style="overflow-y:auto;max-height:15ex">
                        <div 
                            v-for="(tag,index) in allTags" 
                            :key="index"
                            :style="'background:'+colors[tag.type]"
                            class="ownTags"
                            @click="AddExistedTag(tag.name,tag.type)"
                        >
                            {{tag.name}}
                        </div>
                    </div>
                    <div v-else style="text-align:center;padding:0.5ex">
                        您当前没有任何标签！
                    </div>
                </div>
            </el-popover>   
            <div v-if="aid!=-1" style="width:100%;margin-top:2ex">
                <!--文章基本数据-->
                <viewNumSider :aid="aid" style="padding:1ex;background:white;border-radius:1ex;border:1px solid #e8e8e8"></viewNumSider>
            </div>
            <!--团队成员权限修改区-->
            <!--个人模式：不显示-->
            <div v-if="tid!=-1&&aid!=-1" style="border:1px solid #e8e8e8;background:white;margin-top:2ex;border-radius:1ex">
                <h3>成员权限设置</h3>
                <WeightList :tid="tid" :uid="uid" :aid="aid" :authority="this.MyLevel"></WeightList>
            </div>
            <!--外部人员权限设置-->
            <!--两种模式都要显示，只要不是新建-->
            <div v-if="aid!=-1" style="border:1px solid #e8e8e8;background:white;margin-top:2ex;border-radius:1ex">
                <h3>游客权限设置</h3>
                <WeightList2 :level="this.MyLevel" :aid="aid"></WeightList2>
            </div>
        </div>
    </div>
</template>
<script>
import viewNumSider from "@/components/viewNumSider/viewNumSider.vue";
import WeightList from "@/components/WeightList/WeightList.vue"
import WeightList2 from "@/components/WeightList2/WeightList2.vue"
export default {
    components:{
        WeightList,
        WeightList2,
        viewNumSider
    },
    props:{
        Editable:Boolean,//是否有权利修改
        MyLevel:Number,//我的等级
        isTagVisible:Boolean//tag是否可见
    },
    data(){
        return{
            md5:"",
            copys:"无",
            aid:Number,//文章id
            uid:Number,//用户id
            tid:Number,//团队id
            mid:Number,//模板的id
            articleInfo:{//文章信息
                content:"",//文章的内容
                title:"",//题目的内容
            },
            Tags:[],
            inputVisible:false,
            inputValue:{
                name:'',
                type:''
            },
            types:[
                'primary',
                'success',
                'warning',
                'danger',
                'info'
            ],
            checkVisible:1,
            noTag:true,
            colors:{
                primary:"#409eff",
                danger:"#f56c6c",
                info:"#909399",
                success:"#67c23a",
                warning:"#e6a23c"
            },
            allTags:[
                
            ]
        }
    },
    /*刚刚创建的时候 */
    created(){
        this.tid=parseInt(this.$route.params.TID==undefined?"-1":this.$route.params.TID);
        this.aid=parseInt(this.$route.params.AID);
        this.uid=parseInt(this.$route.params.UID);
        this.mid=parseInt(this.$route.params.MID);
        this.AIDgetUID();
        /*判断模板相关 */
        if(this.aid!=-1){//阅读已有的文章，或者根据模板新建
            console.log("不是新建");
            this.getArticle(this.aid);
        }
        else if(this.mid!=-1){
            console.log("模板新建");
            this.getArticle(this.mid);
        }
        /*获取全部的模板 */
        this.getAllTags();
    },
    methods:{
        /*get md5*/
        AIDgetUID(){
            var AID=parseInt(this.aid);
            let param_data = new FormData();
            param_data.append("AID",AID);
            this.$axios
                .post("/api/AIDtoUID/", param_data)
                .then(response => {
                    //this.authorUID=response.data.UID
                    this.md5=response.data.md5
                    this.copys="http://localhost:8080/sharepage/"+this.md5;
                    console.log(response.data)
                }
                )
                .catch(error => {
                  this.$message({
                    message: "",
                    type: "error",
                    customClass: "c-msg",
                    duration: 0,
                    showClose: true,
                    duration: 1000
                  });
                  Promise.reject(error);
                });
        },
        /*选中一个已经通过input新建过或者已经存在的标签 */
        AddExistedTag(tag_name,tag_type){
            var flag = true;//检查重复问题
            for(var i=0;i<this.Tags.length;i++){
                if(tag_name==this.Tags[i].name){
                    flag=false;
                    break;
                }
            }
            if(flag)this.Tags.push({
                name:tag_name,
                type:tag_type
            });
        },
        /*从后端获取全部的标签 */
        getAllTags(){
            let form = new FormData();
            if(this.tid=="-1"){//个人
                form.append("Mode","User");
            }
            else{
                form.append("Mode","Team");
            }
            form.append("UID",this.uid);
            form.append("TID",this.tid);
            this.$axios.post("/api/getTags/",form)
            .then(response=>{
                if(response.data.Status){
                    this.allTags=response.data.tags;
                }
            })
            .catch(error=>{

            })
        },
        /*从后端获取文章/模板信息 */
        getArticle(article_id){
            let form = new FormData();
            form.append("AID",article_id);
            form.append("UID",this.uid);
            this.$axios.post("/api/getArticle/",form)
            .then(response=>{
                if(response.data.Status){//解析成功
                    this.articleInfo.content=response.data.Content;
                    this.articleInfo.title=response.data.Title;
                    this.Tags=response.data.Tags;
                    console.log(this.Tags);
                    console.log("response="+response.data.Tags);
                }
                else{
                    this.$message({
                        message:"文章不存在或已删除",
                        showClose:true,
                        duration:1000,
                        type:"error"
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"服务器好像出了点问题",
                    showClose:true,
                    duration:1000,
                    type:"error"
                })
            })
        },
        /*保存文档，考虑以下几种问题 */
        //创建个人文档
        //更新团队文档
        //创建个人文档
        //更新团队文档
        saveAsMine(type){
            let targetUrl = (type=='doc'?"/api/sendMyArticle/":"/api/sendMyModel/")
            if(type=='model'){//新建模板
                this.aid=-1;
            }
            /*let form = new FormData();
            form.append("UID",this.uid);
            form.append("AID",this.aid);
            form.append("Tags",this.Tags);
            if(this.articleInfo.content!="")form.append("Content",this.articleInfo.content);
            else form.append("Content","暂时还没内容");
            if(this.articleInfo.title!="")form.append("Title",this.articleInfo.title);
            else form.append("Title","暂时还没想好题目");
            form.append("TID",(this.tid=="-1"?"":this.tid));*/
            let T=[];
            for(var i=0;i<this.Tags.length;i++){
                var obj=new Object();
                obj.name=this.Tags[i].name;
                obj.type=this.Tags[i].type;
                T.push(obj);
            }
            if(this.articleInfo.content=="")
                this.articleInfo.content="暂时还没内容";
            if(this.articleInfo.title=="")
                this.articleInfo.title="暂时还没想好题目";
            let form={};
            form={
                TID:(this.tid=="-1"?"":this.tid),
                UID:this.uid,
                AID:this.aid,
                Content:this.articleInfo.content,
                Title:this.articleInfo.title,
                Tags:T
            }
            this.$axios.post(targetUrl,form)
            .then(response=>{
                if(response.data.Status){//发送成功
                    this.$message({
                        message:"发送成功，即将跳转至工作台",
                        duration:1000,
                        showClose:true,
                        type:"success"
                    })
                    this.$router.go(-1);
                }
                else{
                    this.$message({
                        message:response.data.error,
                        duration:1000,
                        showClose:true,
                        type:"error"
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"服务器好像有点问题",
                    duration:1000,
                    showClose:true,
                    type:"error"
                })
            })
        },
        $imgAdd(pos, $file){
            let form = new FormData();
            form.append('img', $file);
            this.$axios.post("/api/picSave/",form,{
                headers:{
                    'Content-Type': 'multipart/form-data'
                }
            }).then(response=>{
                this.$refs.editor.$img2Url(pos,"http://119.45.200.4"+response.data.imgUrl);
            })
        },
        handleClose(tag) {
            this.Tags.splice(this.Tags.indexOf(tag), 1);
        },

        showInput() {
            this.inputVisible = true;
            this.$nextTick(_ => {
            this.$refs.saveTagInput.$refs.input.focus();
            });
        },
        /*通过回车的方式加入一个新的标签，如果这个标签后端存在，则把类别统一，如果已经加入了，那就不管了 */
        handleInputConfirm() {
            console.log("此时inputValue.name="+this.inputValue.name);
            console.log("此时inputValue.type="+this.inputValue.type);
            this.inputValue.type=this.types[this.checkVisible-1];
            var flag=true
            //改变类型
            for(var i=0;i<this.allTags.length;i++){
                if(this.inputValue.name==this.allTags[i].name){
                    this.inputValue.type=this.allTags[i].type;
                    break;
                }
            }
            //去重
            for(var i=0;i<this.Tags.length;i++){
                if(this.inputValue.name==this.Tags[i].name){
                    flag=false;
                    break;
                }
            }
            if (this.inputValue.name) {
                if(flag)this.Tags.push({name:this.inputValue.name,type:this.inputValue.type});
                console.log(this.Tags);
            }
            this.inputVisible = false;
            this.inputValue.name = '';
            this.inputValue.type=''; 
            this.checkVisible=1;
            this.noTag=false;
            
        },
        changeType(type){
            console.log(this.checkVisible);
            this.checkVisible=type;
            if(this.checkVisible==1){
                this.inputValue.type='primary';
            }else if(this.checkVisible==2){
                this.inputValue.type='success';
            }else if(this.checkVisible==3){
                this.inputValue.type='warning';
            }else if(this.checkVisible==4){
                this.inputValue.type='danger';
            }else if(this.checkVisible==5){
                this.inputValue.type='info';
            }             
        }
    }
}
</script>
<style lang="scss">
.tagBox{
    padding:0px;
    border-radius:1ex;
}
</style>
<style>
    .el-tag + .el-tag {
        margin-left: 10px;
    }
    .button-new-tag {
        margin-top:0.5em;
        height: 24px;
        padding-top: 0;
        padding-bottom: 0;
    }
    .input-new-tag {
        width: 90px;
        margin-left: 10px;
        vertical-align: bottom;
    }
    .myList{
        list-style-type: none;
        padding:0px;
        margin-left:10%;
    }
    .myListItem{
        list-style-type: none;
        display: inline-block;
        margin-right:8%
    }
    .blueCircle{
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: #409EFF;
    }
    .greenCircle{
        width: 18px;
        height: 18px;
        border-radius: 50%;
        background-color: #67C23A;
    }
    .yellowCircle{
        width: 18px;
        height: 18px;
        border-radius: 50%;
        background-color: #E6A23C;
    }
    .redCircle{
        width: 18px;
        height: 18px;
        border-radius: 50%;
        background-color: #F56C6C;
    }    
    .greyCircle{
        width: 18px;
        height: 18px;
        border-radius: 50%;
        background-color: #909399;
    }
    .el-icon-check{
        width:18px;
        height:18px;
        color:white;
        margin-left:2px;
    }
    .icon-transform{
        transform: rotate(90deg);
        margin-right: 0.5em;
        margin-left: 0.5em;
    }
    .ownTags{
        text-align:center;
        padding:0.2ex;
        color:white;
        font-weight:normal;
        cursor:pointer;
    }
    .ownTags:hover{
        font-weight: bold;
    }
    .ownTags:active{
        font-weight: bold;
        color:lightgrey;
    }
</style>





