<template>
    <div id="main">
        <!--删除团队的弹出框-->
        <el-dialog
            title="提示"
            :visible.sync="deleteDialogVisible"
            width="30%"
        >
            <span>确认要解散本团队吗？</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="deleteDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="dismiss()">确 定</el-button>
            </span>
        </el-dialog>
        <!--邀请成员的对话框-->
        <el-dialog
            title="提示"
            :visible.sync="inviteDialogVisible"
            width="30%"
        >
            <p>请输入被邀请人邮箱：</p>
            <el-input type="text" v-model="inviteeEmail"></el-input>
            <span slot="footer" class="dialog-footer">
                <el-button @click="inviteDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="invite()">确 定</el-button>
            </span>
        </el-dialog>
        <!--退出团队的对话框-->
        <el-dialog
            title="提示"
            :visible.sync="quitDialogVisible"
            width="30%"
        >
            <span>真的要退出这个团队吗？</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="quitDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="quitTeam()">确 定</el-button>
            </span>
        </el-dialog>
        <!--标题-->
        <div style="border-bottom:1px solid #e8e8e8">
            <h3 style="text-align:left;
                padding-bottom:1ex;
                padding-top:1ex;
                display:inline-block;
                margin:auto auto auto auto;
                width:76%"
            >
                &nbsp;成员权限列表
            </h3>
            <!--按钮区-->
            <div style="display:inline-block;width:15%">
                <i class="el-icon-plus" style="cursor:pointer" @click="inviteDialogVisible=true" v-if="myLevel>=5"></i>
                <i class="el-icon-delete" style="cursor:pointer" @click="deleteDialogVisible=true" v-if="isFounder()"></i>
                <i class="el-icon-right" style="cursor:pointer" @click="quitDialogVisible=true" v-if="!isFounder()"></i>
            </div>
        </div>
        <!--列表项-->
        <div v-for="(member,i) in list" :key="i" style="padding-left:5px;padding-right:5px">
            <!--成员操作的弹窗，目前只有剔除成员功能-->
            <el-popover
                placement="bottom"
                trigger="click"
                :ref="'popMenu'+i"
                v-if="!member.Founder&&myLevel>=5&&uid!=member.UID"
            >
                <p @click="kickout(i,member.UID)" class="popoverOption"><i class="el-icon-close"></i>&nbsp;&nbsp;剔除该成员</p>
            </el-popover>
            <!--头像-->
            <el-avatar v-if="member.AvatarUrl" :src="'http://119.45.200.4'+member.AvatarUrl" :fit="'scale-down'" style="vertical-align:middle;display:inline-block" v-popover="'popMenu'+i"></el-avatar>
            <el-avatar v-else
                icon="el-icon-user-solid" 
                style="vertical-align:middle;display:inline-block"
                v-popover="'popMenu'+i"
            ></el-avatar>
            <!--昵称-->
            <p style="display:inline-block;width:50%;cursor:pointer" @click="goOtherIndex(member.UID)">{{member.Username}}</p>
            <!--权限大小调整-->
            <el-input-number 
                v-model="member.Level" 
                size="mini" 
                style="width:30%" 
                :max="5"
                :min="1"
                @change="submitChanges(i)"
                :disabled="myLevel<5||member.Founder"
            ></el-input-number>
        </div>
    </div>
</template>
<script>
import QS from "qs"
export default {
    name:"memberList",
    props:{
        myLevel:Number,//我的等级
        tid:Number//团队编号
    },
    data(){
        return{
            deleteDialogVisible:false,//删除弹窗是否存在
            inviteDialogVisible:false,//邀请弹窗是否存在
            quitDialogVisible:false,//退出团队弹窗是否存在
            inviteeEmail:"",//被邀请人邮箱
            list:[],
            uid:0,
        }
    },
    created(){
        /*获得成员列表 */
        this.getMemberList();
        this.uid=this.$route.params.UID;
    },
    methods:{
        /*从后端获取全部的成员的列表 */
        getMemberList(){
            let form = new FormData();
            console.log("这个TID怎么回事Undefined呢"+this.tid)
            form.append("TID",this.tid);
            this.$axios.post("/api/memberList/",form)
            .then(response=>{
                this.list=response.data.members;
            })
            .catch(error=>{
                this.$message({
                    message:"服务器好像有点问题",
                    showClose:true,
                    duration:1000,
                    type:"error"
                })
            })
        },
        /*提交做出的更改 */
        submitChanges(i){
            let form = new FormData();
            form.append("TID",this.tid);
            form.append("UID",this.list[i].UID);
            form.append("Level",this.list[i].Level);
            this.$axios.post("/api/modifyRECD/",form)
            .then(response=>{
                if(response.data.Status){//修改成功
                    this.$message({
                        message:"成功修改"+this.list[i].Username+"的权限",
                        type:"success",
                        showClose:true,
                        duration:1000
                    })
                }
                else{
                    this.$message({
                        message:"修改失败，请稍后提交",
                        type:"error",
                        showClose:true,
                        duration:1000
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"modifyRECD好像有点问题",
                    type:"error",
                    showClose:true,
                    duration:1000
                })
            })
        },
        /*邀请成员 */
        invite(){
            this.inviteDialogVisible=false;
            if(this.inviteeEmail=="")return;
            let form = new FormData();
            form.append("TID",this.tid);
            form.append("TargetEmail",this.inviteeEmail);
            this.$axios.post("/api/inviteMember/",form)
            .then(response=>{
                if(!response.data.Status){//邀请不成功
                    if(response.data.Exist){//这人已经在团队里了
                        this.$message({
                            message:"该成员已存在",
                            duration:1000,
                            showClose:true,
                            type:"error"
                        })
                    }
                    else{//服务器有点问题
                        this.$message({
                            message:"用户不存在",
                            duration:1000,
                            showClose:true,
                            type:"error"
                        })
                    }
                }
                else{
                    this.$message({
                        message:"邀请成功",
                        duration:1000,
                        showClose:true,
                        type:"success"
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"/api/inviteMember/好像有点问题",
                    duration:1000,
                    showClose:true,
                    type:"error"
                })
            })
        },
        /*解散团队 */
        dismiss(){
            this.deleteDialogVisible=false;
            let form = new FormData();
            form.append("TID",this.tid);
            this.$axios.post("/api/dismissTeam/",form)
            .then(response=>{
                if(response.data.Status){//解散成功
                    this.$message({
                        message:"成功解散团队",
                        type:"success",
                        duration:1000,
                        showClose:true
                    })
                    this.$router.push({
                        path:"/craftTable/"+this.$store.state.userProfile.UID
                    })
                }
                else{
                    this.$message({
                        message:"解散失败，团队已经被解散",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"dismissTeam好像出了点问题",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
            })
        },
        /*剔除成员 */
        kickout(i,target){
            let form = new FormData();
            form.append("TID",this.tid);
            form.append("UID",target);
            this.list.splice(i,1);
            this.$axios.post("/api/kickMember/",form)
            .then(response=>{
                if(response.data.Status){
                    this.$message({
                        message:"剔除成功",
                        type:"success",
                        duration:1000,
                        showClose:true
                    })
                }
                else{
                    this.$message({
                        message:"服务器忙，请稍后再试",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"kickMember好像有点问题",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
            })
        },
        /*判断当前用户是不是创始人 */
        isFounder(){
            for(var i=0; i<this.list.length;i++){
                if(this.$store.state.userProfile.UID==this.list[i].UID&&this.list[i].Founder){
                    return true;
                }
            }
            return false;
        },
        /*退出团队 */
        quitTeam(){
            let form = new FormData();
            form.append("UID",this.$store.state.userProfile.UID);
            form.append("TID",this.tid);
            this.$axios.post("/api/quitTeam/",form)
            .then(response=>{
                if(response.data.Status){//退出队伍成功
                    this.$message({
                        message:"退出团队成功",
                        type:"success",
                        duration:1000,
                        showClose:true
                    })
                    this.$router.replace({
                        path:"/craftTable/"+this.$store.state.userProfile.UID,
                    })
                }
                else{
                    this.$message({
                        message:"服务器忙，请待会再退出",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"/api/quitTeam/好像有点问题",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
            })
        },
        goOtherIndex(uid){
            this.$router.push("/OtherIndex/"+uid);
        }
    }
}
</script>
<style scoped>
.popoverOption{
    cursor:pointer;
    padding:1ex 1ex 1ex 1ex;
    margin:auto 0px auto 0px;
}
.popoverOption:hover{
    font-weight: bold;
    background:#e8e8e8;
}
</style>


