<template>
<div>
    <div>
        <myheader :showAvatar="true" :showSearch="false"></myheader>
    </div>
    <div class="mainStyle">
        <div style="width:40%;margin-left:30%;">
            <el-breadcrumb separator-class="el-icon-arrow-right" style="margin:10px">
                <!--<el-breadcrumb-item><span><a href="/">首页</a></span></el-breadcrumb-item>-->
                <el-breadcrumb-item style="cursor:pointer"><span @click="tocraft" style="">前页</span></el-breadcrumb-item>
                <el-breadcrumb-item>个人信息</el-breadcrumb-item>
            </el-breadcrumb>
            <el-card class="box-card">
                        <!--头像-->
                        <div id="avatar" style="margin:10px">
                            <el-avatar v-if="imageUrl" :src="'http://119.45.200.4'+imageUrl"></el-avatar>
                            <el-avatar v-else icon="el-icon-user-solid"></el-avatar>
                        </div>
                        <hr style="width:70%">
                        
                        <!--用户名-->
                        <div id="email" style="width:100%;text-align:left;font-size:90%">
                            <p>用户名<span style="margin-left:22%">{{this.name}}</span></p>
                        </div>                        


                        <!--邮箱-->
                        <div id="email" style="width:100%;text-align:left;font-size:90%">
                            <p>邮箱<span style="margin-left:22%">{{this.email}}</span></p>
                        </div>
            </el-card>
        </div>
    </div>
</div>
</template>

<script>
import QS from "qs";
import myheader from '../myHeader/myHeader.vue';
export default {
    name:"OtherIndex",
    components:{
        myheader
    },
    data(){
        return{
            imageUrl:"",  
            name:"",
            email:"",
            uid:this.$route.params.UID
        }
    },
    mounted:function () {
        console.log(this.uid);
        var param_data={
            user_id:this.uid
        }
        this.$axios.post("/api/PersonIndex/",QS.stringify(param_data))
        .then(response=>{
            this.name=response.data.Username;
            this.email=response.data.Email;
            this.imageUrl=response.data.imageUrl;
        })
    },
    methods:{
        tocraft(){
            this.$router.go(-1);
        }
    }
}
</script>
<style scoped>
.mainStyle{
    display:flex;
    justify-items: center;
    align-items: center;    
    min-height: 80vh;
    background-color: #f7f7f7;
}
.cardStyle{
    display:flex;
    justify-items: center;
    align-items: center;
}
</style>


