<template>
    <!--总体-->
    <div id="main" style="text-align:center;padding-top:5%">
        <!--属于logo的div-->
        <div id="logo">
            <img src="/static/logo.png" style="width:10%"/>
        </div>
        <div>
            <p class="logo">金&nbsp;刚&nbsp;石&nbsp;文&nbsp;档</p>
        </div>
        <!--登录注册区-->
        <div 
            style="
                width:100%;
                text-algin:center;
                margin-right:auto;
            "
        >
            <!--选项卡-->
            <div style="margin:auto;width:30%;">
                <div 
                    class="selected" 
                    style="display:inline-block;width:40%;cursor:pointer" 
                    id="login_tab"
                    @click="select('login_tab')"
                >
                    <p style="margin:10px auto 10px auto">登录</p>
                </div>
                <div 
                    class="option" 
                    style="display:inline-block;width:40%;cursor:pointer" 
                    id="register_tab"
                    @click="select('register_tab')"
                >
                    <p style="margin:10px auto 10px auto">注册</p>
                </div>
                
            </div>
            <login style="width:30%;margin:auto;box-shadow:2ex 2ex 3ex #e8e8e8" v-if="cur_show=='login_tab'"></login>
            <register style="width:30%;margin:auto;box-shadow:2ex 2ex 3ex #e8e8e8" v-else></register>
        </div>
    </div>
</template>
<script>
import login from "@/components/login/login.vue"
import register from "@/components/register/register.vue"
export default {
    data(){
        return{
            cur_show:"login_tab"
        }
    },
    components:{
        login,
        register
    },
    created(){
    },
    methods:{
        select(target){
            if(this.cur_show!=target){
                this.cur_show=(this.cur_show=="login_tab"?"register_tab":"login_tab");
            }
            /*把另一个变成普通的 */
            let selectedTag = document.getElementsByClassName("selected");
            if(selectedTag!=null){
                var t;
                for(t=0;t<selectedTag.length;t++){
                    selectedTag[t].setAttribute("class","option");
                }
            }
            /*把当前变成selected */
            document.getElementById(target).setAttribute("class","selected");
        }
    }
}
</script>
<style scoped>
.logo{
    margin-top:0px;
    margin-bottom: 0px;
    font-size:250%;
    font-weight:bold;
}
.option{
    font-size:150%;
    font-weight: normal;
}
.option:hover{
    font-size:150%;
    font-weight: bold;
    border-bottom:2px solid lightblue
}
.option:active{
    font-size: 150%;
    font-weight: bold;
    border-bottom: 2px solid blue;
}
.selected{
    font-size: 150%;
    font-weight: bold;
    border-bottom: 3px solid blue;
}
</style>


