<template>
    <div style="padding:1ex">
		<!--四个权限的设置-->
        <div style="width:100%;text-align:center">
            <p class="authTitle"><i class="el-icon-view"></i>&nbsp;阅读</p>
            <el-input-number 
                :disabled="this.authority<5" 
                size="mini" 
                v-model="Rnum" 
                controls-position="right" 
                :min="1" 
                :max="5"
                style="width:65%;display:inline-block"
            >	
            </el-input-number>
        </div>
        <div style="width:100%;text-align:center;margin-top:1ex">
            <p class="authTitle"><i class="el-icon-chat-round"></i>&nbsp;评论</p>
            <el-input-number 
                :disabled="this.authority<5" 
                size="mini" 
                v-model="Cnum" 
                controls-position="right" 
                :min="1" 
                :max="5"
                style="width:65%;display:inline-block"
            >
            </el-input-number>
        </div>
        <div style="width:100%;text-align:center;margin-top:1ex">
            <p class="authTitle"><i class="el-icon-edit"></i>&nbsp;编辑</p>
            <el-input-number 
                :disabled="this.authority<5" 
                size="mini" 
                v-model="Enum" 
                controls-position="right" 
                :min="1" 
                :max="5"
                style="width:65%;display:inline-block"
            >
            </el-input-number>
        </div>
        <div style="width:100%;text-align:center;margin-top:1ex">
            <p class="authTitle"><i class="el-icon-delete"></i>&nbsp;删除</p>
            <el-input-number 
                :disabled="this.authority<5" 
                size="mini" 
                v-model="Dnum" 
                controls-position="right" 
                :min="1" 
                :max="5"
                style="width:65%;display:inline-block"
            >
            </el-input-number>
        </div>
		<!--确认修改-->
		<el-button plain style="margin-top:1.5ex;margin-bottom:0.5ex;width:50%" @click="submitForm()">确认修改</el-button>
    </div>
</template>

<script>
import QS from "qs";
const clickoutside = {
    update() {},
};
export default {
    props:{
      tid:Number,
      uid:Number,
      aid:Number,
      authority:Number,
    },
    name:'WeightList',
    data(){
        return{
            Rnum: Number,
            Cnum: Number,
            Enum: Number,
            Dnum: Number,
            type:'A',
        }
    },
    directives: {clickoutside},
    mounted: function() {
        var param_datad = {aid: this.aid};
        console.log(QS.stringify(param_datad))
          this.$axios.post("/api/getauthority1/", QS.stringify(param_datad))
              .then(response => {
                  // post 成功，response.data 为返bai回的数据
                  this.Rnum=response.data.Rnum;
                  this.Cnum=response.data.Cnum;
                  this.Enum=response.data.Enum;
                  this.Dnum=response.data.Dnum;
                  console.log(response.data);
                  /*console.log(response.data.email)
                  console.log(response.data.mobile)
                  console.log(this.form.need_name)*/
              })
              .catch(error => {
                // 请求失败du
                console.log(error)
           })
    },
    methods: {
      toProfile() {
        this.$router.push({ 
          path : "/user/profile",
          query: {
            UID : parseInt(this.form.uid),
            Judge:1
          }
        });
      },
      submitForm() {
                let param_data2={
                    aid: this.aid,//文章ID
                    Rnum: this.Rnum,//1-0读
                    Cnum: this.Cnum,//1-0评论
                    Enum: this.Enum,//1-0写
                    Dnum: this.Dnum,//1-0删
                }
                 this.$axios
                    .post("/api/pushauthority1/",QS.stringify(param_data2))
                    .then((response) =>{
                      console.log(this.content);
                      if(response.data.feedback){
                          this.$message({
                          message:
                            "修改成功",
                          type: "success",
                          customClass: "c-msg",
                          duration: 1000
                        });
                      }
                      else{
                          this.$message({
                          message: "修改失败",
                          type: "error",
                          customClass: "c-msg",
                          showClose: true,
                          duration: 1000
                        }); 
                      }
                  })
                  .catch(error => {
                    console.log("!!!")
                    this.isLoading = false;
                    this.$message({
                      message: "SubmitForm出了点状况",
                      type: "error",
                      customClass: "c-msg",
                      duration: 0,
                      showClose: true,
                      duration: 1000
                    });
                    Promise.reject(error);
                  });
              },
    }    
}
</script>
<style scoped>
.authTitle{
	width:30%;
	font-weight:bold;
	text-align:left;
	display:inline-block;
	font-size:120%;
	margin:auto auto auto auto;
}
</style>



