<template>
<div>
    <div>
        <myheader :showAvatar="true" :showSearch="false"></myheader>
    </div>
    <div class="mainStyle">
        <div style="width:40%;margin-left:30%;">
            <el-breadcrumb separator-class="el-icon-arrow-right" style="margin:10px">
                <!--<el-breadcrumb-item><span><a href="/">首页</a></span></el-breadcrumb-item>-->
                <el-breadcrumb-item style="cursor:pointer"><span @click="tocraft" style="">前页</span></el-breadcrumb-item>
                <el-breadcrumb-item>个人信息</el-breadcrumb-item>
            </el-breadcrumb>
            <el-card class="box-card">
                <el-tabs :tab-position="tabPosition" style="height:200px;">
                    <el-tab-pane label="用户信息">
                        <el-upload
                            action="/api/avatarUrl/"
                            :data="p_data"
                            :show-file-list="false"
                            :on-success="handleAvatarSuccess"
                            :before-upload="beforeAvatarUpload">
                            <div id="avatar" style="margin:10px">
                                <el-avatar v-if="imageUrl" :src="imageUrl" :fit="'scale-down'"></el-avatar>
                                <el-avatar v-else icon="el-icon-user-solid"></el-avatar>
                            </div>
                        </el-upload>
                        <hr style="width:70%">

                        <!--用户名,需要实现点击输入框以外的地方即可修改用户名-->
                        <el-form ref="form" :model="form" label-width="10%" size="small" label-position="left" >
                            <el-form-item label="昵称" prop="name">
                                <el-input v-model="form.name"
                                @blur="validateName"
                                style="width:60%"></el-input>
                            </el-form-item>
                        </el-form>

                        <!--邮箱-->
                        <div id="email" style="width:100%;text-align:left;font-size:90%">
                            <p>邮箱<span style="margin-left:22%">{{this.form.email}}</span></p>
                        </div>

                    </el-tab-pane>

                    <el-tab-pane label="修改密码">
                        <el-form ref="form" :model="form" label-width="20%" size="small" label-position="left">
                            <el-form-item label="当前密码" prop="old_password">
                                <el-input v-model="form.old_password"
                                type="password"
                                style="width:60%"></el-input>
                            </el-form-item>
                            <el-form-item label="新密码" prop="new_password">
                                <el-input v-model="form.new_password"
                                type="password"
                                style="width:60%"></el-input>
                            </el-form-item>
                            <el-form-item label="确认密码" prop="new_password_confirmation">
                                <el-input v-model="form.new_password_confirmation"
                                type="password"
                                style="width:60%"></el-input>
                            </el-form-item>
                            <el-form-item size="mini">
                                <el-button type="primary" style="background-color:#414648;border:2px solid #414648"
                                @click="validatePassword">确认更改
                                </el-button>
                            </el-form-item>
                        </el-form>
                    </el-tab-pane>

                    <el-tab-pane label="修改邮箱">
                        <el-form ref="form" :model="form" label-width="15%" size="small" label-position="left">
                            <el-form-item label="新邮箱" prop="email">
                                <el-input v-model="form.email"
                                style="width:60%"></el-input>
                            </el-form-item>
                            <el-form-item label="验证码">
                                <el-input v-model="form.captcha"
                                style="width:37%;align:right"></el-input>
                                <el-button type="primary" style="background-color:#414648;border:2px solid #414648;width:23%;text-align:center" size="mini" @click="sendVerify">发送验证码
                                </el-button>
                            </el-form-item>
                            <el-form-item size="mini">
                                <el-button type="primary" style="background-color:#414648;border:2px solid #414648"
                                @click="checkEmail">确认更改
                                </el-button>
                            </el-form-item>
                        </el-form>
                    </el-tab-pane>
                </el-tabs>
            </el-card>
        </div>
    </div>
</div>
</template>

<script>
import QS from "qs";
import myheader from '../myHeader/myHeader.vue';
export default {
    name:"PersonIndex",
    components:{
        myheader
    },
    data(){
        return{
            imageUrl:"",   //改为通过store获取
            p_data:{UID:this.$store.state.userProfile.UID},
            tabPosition:'left',
            isChangeName:false,
            isChangePass:false,
            isChangeEmail:false,
            form:{
                name:"",
                email:"",
                old_password:"",
                new_password:"",
                new_password_confirmation:"",
                captcha:""
            }
        }
    },
    mounted:function () {
        console.log(this.imageUrl);
        var uid=this.$store.state.userProfile.UID;
        var param_data={
            user_id:uid
        }
        this.$axios.post("/api/PersonIndex/",QS.stringify(param_data))
        .then(response=>{
            this.form.name=response.data.Username;
            this.form.email=response.data.Email;
            if(response.data.imageUrl==""){
                this.imageUrl=response.data.imageUrl;
            }else{
                this.imageUrl="http://119.45.200.4"+response.data.imageUrl;
                this.$store.commit("changeAvatarUrl",this.imageUrl);
            }
          console.log(response.data.imageUrl);
          console.log(this.imageUrl);
        })
    },
    methods:{
        tocraft(){
            this.$router.go(-1);
        },
        handleAvatarSuccess(res,file){

            var uid=this.$store.state.userProfile.UID;
            var param_data={
                user_id:uid
            }
            this.$axios.post("/api/PersonIndex/",QS.stringify(param_data))
            .then(response=>{
                this.imageUrl="http://119.45.200.4"+response.data.imageUrl;
                this.$store.commit("changeAvatarUrl",this.imageUrl);
                this.$router.go(0);
                console.log(this.$store.state.userProfile.avatarUrl);
            })
        },
        beforeAvatarUpload(file){
            const ispic=(file.type==="image/jpeg");
            const isLt2M=file.size/1024/1024<2;
            console.log(file.size);
            if(!ispic){
                this.$message.error('上传头像图片只能是JPG格式！');
            }
            if(!isLt2M){
                this.$message.error('上传头像图片大小不能超过2MB!');
            }
            return ispic&&isLt2M;
        },
        validateName(){
            if(this.form.name==""){
                this.$message({
                    message:"用户名不得为空",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
            }else{
                var param_data={Username:this.form.name};
                this.$axios.post("/api/checkUsername/",QS.stringify(param_data))
                .then(response=>{
                    if(response.data.Used){
                        this.$message({
                            message:"该用户名已被使用",
                            type:"error",
                            duration:1000,
                            showClose:true
                        })
                    }else{
                        this.isChangeName=true;
                        console.log("用户名的修改被触发了");
                        this.submitForm();
                    }
                })
            }
        },
        validatePassword(){
            var i;
            var hasCapital=false;
            var hasNumber=false;
            var hasNormal=false;
            for(i=0;i<this.form.new_password.length;i++){
                if(this.form.new_password.charAt(i)>='a'&&this.form.new_password.charAt(i)<='z')
                    hasNormal=true;
                if(this.form.new_password.charAt(i)>='0'&&this.form.new_password.charAt(i)<='9')
                    hasNumber=true;
                if(this.form.new_password.charAt(i)>='A'&&this.form.new_password.charAt(i)<='Z')
                    hasCapital=true;                
            }
            if(this.form.new_password!=this.form.new_password_confirmation){
                this.$message({
                    message:"两次输入的密码不一致",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
            }
            else if(!hasNumber){
                this.$message({
                    message:"密码太简单，应含有至少一个数字",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
            }
            else if(!hasCapital){
                this.$message({
                    message:"密码太简单，应含有至少一个大写字母",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
            }
            else if(!hasNormal){
                this.$message({
                    message:"密码太简单，应含有至少一个小写字母",
                    type:"error",
                    duration:1000,
                    showClose:true
                })                
            }
            else{
                var uid=this.$store.state.userProfile.UID;
                var param_data={
                    UID:uid,
                    old_password:this.$md5(this.form.old_password)
                };
                this.$axios.post("/api/checkPassword/",QS.stringify(param_data))
                .then(response=>{
                    if(response.data.validation){
                        this.isChangePass=true;
                        this.submitForm();
                    }
                    else{
                       this.$message({
                        message:"原密码输入错误",
                        type:"error",
                        duration:1000,
                        showClose:true
                        })
                    }
                })
            }
        },
        sendVerify(){
            var emreg=/^\w{3,}(\.\w+)*@[A-z0-9]+(\.[A-z]{2,5}){1,2}$/;
            if(!emreg.test(this.form.email)){
                this.$message({
                    message:"邮箱格式有误",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
            }else{
                var theEmail={Email:this.form.email};
                this.$axios.post("/api/captcha/",QS.stringify(theEmail))
                .then(response=>{
                    if(response.data.Status){
                        this.$message({
                            message:"验证码发送成功，请检查您的邮箱，验证码10分钟内有效",
                            type:"success",
                            duration:1000,
                            showClose:true
                        })
                    }else{
                        console.log(this.form.email);
                        this.$message({
                            message:"验证码发送失败，请检查您填写的邮箱地址",
                            type:"error",
                            duration:1000,
                            showClose:true
                        })
                    }
                }).catch(error=>{
                    this.$message({
                        message:"服务器可能有点问题",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
                })
            }
        },
        checkEmail(){
            if(this.form.captcha==""){
                this.$message({
                    message:"验证码不能为空",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
            }else{
                this.isChangeEmail=true;
                this.submitForm();
            }
        },
        submitForm(){
            var uid=this.$store.state.userProfile.UID;
            var param_data={
                UID:uid,
                username:"",
                email:"",
                captcha:"",
                new_password:""
            }
            if(this.isChangeName){
                param_data.username=this.form.name;
                this.$axios.post("/api/changeInfo/",QS.stringify(param_data))
                .then(response=>{
                    if(response.data.validation){
                        this.$message({
                            message:"用户名修改成功",
                            type:"success",
                            duration:1000,
                            showClose:true
                        })
                        this.form.name=response.data.username_new;
                        this.form.email=response.data.email_new;
                    }else{
                        this.$message({
                            message:"用户名修改失败",
                            type:"error",
                            duration:1000,
                            showClose:type
                        })
                    }
                }).catch(error=>{
                    this.$message({
                        message:"服务器ERROR",
                        type:"error",
                        duration:1000,
                        showClose:type
                    })
                })
                this.isChangeName=false;
            }else if(this.isChangePass){
                param_data.new_password=this.$md5(this.form.new_password);
                this.$axios.post("/api/changeInfo/",QS.stringify(param_data))
                .then(response=>{
                    if(response.data.validation){
                        this.$message({
                            message:"密码修改成功",
                            type:"success",
                            duration:1000,
                            showClose:true
                        })
                        this.form.name=response.data.username_new;
                        this.form.email=response.data.email_new;
                        this.form.old_password="";
                        this.form.new_password="";
                        this.form.new_password_confirmation="";
                    }else{
                        this.$message({
                            message:"密码修改失败",
                            type:"error",
                            duration:1000,
                            showClose:type
                        })
                    }
                }).catch(error=>{
                    this.$message({
                        message:"服务器ERROR",
                        type:"error",
                        duration:1000,
                        showClose:type
                    })
                })
                this.isChangePass=false;
            }else if(this.isChangeEmail){
                param_data.email=this.form.email;
                param_data.captcha=this.form.captcha;
                this.$axios.post("/api/changeInfo/",QS.stringify(param_data))
                .then(response=>{
                    if(response.data.validation){
                        this.$message({
                            message:"邮箱修改成功",
                            type:"success",
                            duration:1000,
                            showClose:true
                        })
                        this.$router.go(0);
                        this.form.name=response.data.username_new;
                        this.form.email=response.data.email_new;
                    }else{
                        this.$message({
                            message:"邮箱修改失败",
                            type:"error",
                            duration:1000,
                            showClose:type
                        })
                    }
                }).catch(error=>{
                    this.$message({
                        message:"服务器ERROR",
                        type:"error",
                        duration:1000,
                        showClose:type
                    })
                })
                this.isChangeEmail=false;
            }
        }
    }
}
</script>
<style scoped>
.mainStyle{
    display:flex;
    justify-items: center;
    align-items: center;
    min-height: 80vh;
    background-color: #f7f7f7;
}
.cardStyle{
    display:flex;
    justify-items: center;
    align-items: center;
}
</style>



