<template>
    <div>
        <myHeader :showAvatar="showavatar" :showSearch="showsearch"></myHeader>
        <!--左栏-->
        <div style="display:inline-block;width:10%;vertical-align:top">
            <LeftSliderPro :team="teams" style="width: 100%;"></LeftSliderPro>
        </div>
        <!--右栏-->
        <div style="display:inline-block;width:70%;vertical-align:top">
            <!--选项卡，区分编辑区和评论区-->
            <div style="width:75%;margin-left:2.5%;text-algin:center">
                <el-tabs
                    v-if="this.$route.params.AID!=-1"
                    v-model="activeName"
                    @tab-click="handleClick"
                    style="width:30%;display:inline-block"
                    :stretch="true"
                >
                    <el-tab-pane label="文本编辑" name="shareEditor" style="text-align:center"></el-tab-pane>
                    <el-tab-pane label="讨论交流" name="comment" style="text-align:center"></el-tab-pane>
                </el-tabs>
            </div>
            <!--编辑器-->
            <shareEditor v-if="(!isNaN(this.uid))&&this.uid!=null" v-show="activeName=='shareEditor'" style="width:100%" :Editable="CanEdit" :MyLevel="1"></shareEditor>
            <comment 
                v-if="(!isNaN(this.uid))&&this.uid!=null"
                v-show="activeName=='comment'"
                :myuid="this.$store.state.userProfile.UID"
                :AID="''"
                :md5="this.$route.params.AID"
                :commentlevel="CanComment?1:4"
                :level="CanComment?4:1"
                style="width:75%"
            >
            </comment>
        </div>
    </div>
</template>
<script>
import leftSider from "@/components/LeftSider/LeftSider.vue"
import myHeader from "@/components/myHeader/myHeader.vue"
import shareEditor from "@/components/sharepage/shareEditor.vue"
import comment from "@/components/Comment/Comment.vue"
import LeftSliderPro from "../../components/LeftSliderPro/LeftSliderPro";
export default {
    components:{
        myHeader,
        shareEditor,
        comment,
        leftSider,
        LeftSliderPro
    },
    data(){
        return{
            md5:"",
            activeName:"shareEditor",
            teams:[],//团队列表
            showavatar:true,
            showsearch:false,
            aid:Number,
            tid:Number,
            PrivateOwn:true,//这篇文章是不是这个用户私有？（且不属于任何团队）如果是，后面的属性无意义
            TeamOwn:true,//这篇文章是不是团队所有，且用户属于这个团队？如果是，后面的属性无意义
            CanRead:true,//这篇文章用户能不能阅读
            CanComment:true,//这篇文章用户能否评论
            CanEdit:true,
            uid:Number,
            articleInfo:{//文章信息
                content:"",//文章的内容
                title:"",//题目的内容
            },
            MyLevel:Number,//我的等级
            Authority:true
        }
    },
    created(){
        //this.aid=parseInt(this.$route.params.AID);
        this.md5=this.$route.params.AID;
        this.uid=parseInt(this.$route.params.UID);
        /*判断模板相关 */
        if(isNaN(this.uid)||this.uid==null){
            window.sessionStorage.setItem("ToRead",this.md5);
            console.log(sessionStorage.getItem("ToRead"));
            this.toSignIn()
        }
        else {
            this.getTeamInfo();
            this.Md5getAID()
        }
    },
    methods: {
        Md5getAID(){
            let form = new FormData();
            form.append("md5",this.md5);
            this.$axios.post("/api/AIDgetMD5/",form)
            .then(response=>{
                if(response.data.feedback){//解析成功
                    this.aid=response.data.AID;
                    /*判断模板相关 */
                    if(isNaN(this.uid)||this.uid==null){
                        window.sessionStorage.setItem("ToRead",this.md5);
                        console.log(sessionStorage.getItem("ToRead"));
                        this.toSignIn()
                    }
                    else {
                        this.AIDgetTID();
                        this.getVisitAuthority();
                    }
                }
                else{
                    this.$message({
                        message:"文章不存在或已删除",
                        showClose:true,
                        duration:1000,
                        type:"error"
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"服务器好像出了点问题",
                    showClose:true,
                    duration:1000,
                    type:"error"
                })
            })
        },
        getArticle(article_id){
            let form = new FormData();
            form.append("AID",article_id);
            form.append("UID",this.uid);
            this.$axios.post("/api/getArticle/",form)
            .then(response=>{
                if(response.data.Status){//解析成功
                    this.articleInfo.content=response.data.Content;
                    this.articleInfo.title=response.data.Title;
                }
                else{
                    this.$message({
                        message:"文章不存在或已删除",
                        showClose:true,
                        duration:1000,
                        type:"error"
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"服务器好像出了点问题",
                    showClose:true,
                    duration:1000,
                    type:"error"
                })
            })
        },
        AIDgetTID(){
            let form = new FormData();
            form.append("AID", this.aid);
            this.$axios.post("/api/AIDgetTID/",form)
                .then(response=>{
                    this.tid=response.data.TID
                })
        },
        handleClick(tab, event) {
            console.log(tab, event);
        },
        /*从后端获取团队列表 */
        getTeamInfo(){
            let form = new FormData();
            form.append("UID", this.$store.state.userProfile.UID);
            this.$axios.post("/api/teamList/",form)
            .then(response=>{
                if(response.data.Status){//检索成功
                    var list = response.data.teams;
                    for(var i in list){
                        console.log(i);
                        this.teams.push({
                            TID:list[i].TID,
                            teamName:list[i].teamName
                        });
                    }
                }
                else{
                    this.$message({
                        message:"获取信息失败",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"服务器好像出了点问题",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
            })
        },
        getVisitAuthority(){
            let form = new FormData();
            form.append("AID",this.aid);
            form.append("UID",this.uid);
            this.$axios.post("/api/authJudger/",form)
            .then(response=>{
                if(response.data.Status){//解析成功
                    this.PrivateOwn=response.data.PrivateOwn//这篇文章是不是这个用户私有？（且不属于任何团队）如果是，后面的属性无意义
                    this.TeamOwn=response.data.TeamOwn//这篇文章是不是团队所有，且用户属于这个团队？如果是，后面的属性无意义
                    this.CanRead=response.data.CanRead//这篇文章用户能不能阅读
                    this.CanComment=response.data.CanComment//这篇文章用户能否评论
                    this.CanEdit=response.data.CanEdit
                    console.log(this.TeamOwn)
                    if(this.PrivateOwn){
                        this.$message({
                            message:
                            "您就是作者！快快请坐",
                            type: "success",
                            customClass: "c-msg"
                        });
                        this.$router.push({
                            path : "/Edit/"+parseInt(this.uid)+"/"+parseInt(this.aid)+"/"+parseInt(-1)
                        });
                    }
                    else if(this.TeamOwn){
                        this.$message({
                            message:
                            "欢迎来到你的团队空间",
                            type: "success",
                            customClass: "c-msg"
                        });
                        this.$router.push({
                            //path : "/Edit/6/38/-1/19"
                            path : "/Edit/"+parseInt(this.uid)+"/"+parseInt(this.aid)+"/"+parseInt(-1)+"/"+parseInt(this.tid)
                        });
                    }
                    else{
                        if(!this.CanRead){//不可阅读，返回工作台
                            this.$message({
                                message:"对不起，您的权限不足以查看这篇文章",
                                type:"error",
                                duration:1000,
                                showClose:true
                            })
                            this.$router.push("/craftTable/"+this.$store.state.userProfile.UID);
                        }
                        else{
                            this.getArticle(this.aid);
                        }
                    }
                }
                else{
                    this.$message({
                        message:"该文章没有设置权限",
                        showClose:true,
                        duration:1000,
                        type:"error"
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"服务器好像出了点问题1111",
                    showClose:true,
                    duration:1000,
                    type:"error"
                })
            })
        },
        toSignIn(){
          this.$message({
            message:
              "游客没有权限查看这篇文章，请先登录",
            type: "success",
            customClass: "c-msg"
          });
          this.$router.push({
            path : "/"
          });
        },
        $imgAdd(pos, $file){
            let form = new FormData();
            form.append('img', $file);
            this.$axios.post("/api/picSave/",form,{
                headers:{
                    'Content-Type': 'multipart/form-data'
                }
            }).then(response=>{
                this.$refs.shareEditor.$img2Url(pos,"http://119.45.200.4"+response.data.imgUrl);
            })
        },
    }
}
</script>
<style scoped>

</style>



