import Vue from 'vue'
import Router from 'vue-router'
import PersonIndex from "@/components/PersonIndex/PersonIndex.vue";
import OtherIndex from "@/components/OtherIndex/OtherIndex.vue";
import WorkRight from "@/components/WorkRight/WorkRight.vue";
import 'font-awesome/css/font-awesome.min.css';
import 'font-awesome/css/font-awesome.min.css';
import Comment from "@/components/Comment/Comment.vue";
import Test1 from "../components/Test1";
import myTeam from "@/views/myTeam/myTeam";
import teamRecycleBin from "@/views/teamRecycleBin/teamRecycleBin";
import testPage from "@/views/testPage.vue"
import frontPage from "@/views/frontPage/frontPage.vue"
import myDocEditor from "@/views/myDocEditor/myDocEditor.vue"
import teamDocEditor from "@/views/teamDocEditor/teamDocEditor.vue"
import sharepage from "@/components/sharepage/sharepage.vue"
import viewNumSider from "@/components/viewNumSider/viewNumSider.vue"
import myDocs from "../views/myDocs/myDocs";
import personalRecycleBin from "../views/personalRecycleBin/personalRecycleBin";

import craftTable from "@/views/craftTable/craftTable";
Vue.use(Router)

export default new Router({
  routes: [
    // 先在上方引入Home的vue对象，然后作为组件使用
    {
      path: "/PersonIndex",
      name: "PersonIndex",
      component: PersonIndex,
    },
    {
      path:"/WorkRight",
      name:"WorkRight",
      component:WorkRight
    },
    // 先在上方引入Home的vue对象，然后作为组件使用
    {
      path: "/Test1",
      name: "Test1",
      component: Test1,
    },
    {
      path:"/craftTable/:UID",
      name:"craftTable",
      component:craftTable
    },
    {
      path:"/myDocs/:UID",
      name:"myDocs",
      component:myDocs
    },
    {
      path:"/Edit/:UID/:AID/:MID",
      name:"Edit",
      component:myDocEditor
    },
    {
      path:"/Comment",
      name:"Comment",
      component: Comment,
    },
    {
      path:"/Edit/:UID/:AID/:MID/:TID",
      name:"teamEdit",
      component:teamDocEditor
    },
    {
      path:"/myTeam/:UID/:TID",
      name:"myTeam",
      component:myTeam
    },
    {
      path:"/personalRecycleBin/:UID/",
      name:"personalRecycleBin",
      component:personalRecycleBin
    },
    {
      path:"/teamRecycleBin/:TID",
      name:"teamRecycleBin",
      component:teamRecycleBin
    },
    {
      path:"/",
      name:"front",
      component:frontPage
    },
    {
      path:`/Test/`,
      name:"Test",
      component:testPage
    },
    {
      path:"/OtherIndex/:UID",
      name:"OtherIndex",
      component:OtherIndex
    },
    {
      path:"/sharepage/:UID/:AID",
      name:"sharepage",
      component: sharepage,
    },
    {
      path:"/sharepage/:AID",
      name:"sharepage2",
      component: sharepage,
    },
    {
      path:"/viewNumSider",
      name:"viewNumSider",
      component: viewNumSider,
    },
  ]
})



