<template>
    <div>
        <h3 style="padding:1ex;margin:auto auto auto auto;border-bottom:1px solid #e8e8e8">
            <span style="width:80%">文章信息</span>
            <!--分享链接-->
            <el-popover
                v-if="this.aid!=-1"
                placement="top-end"
                title="分享链接"
                width="500"
                trigger="hover"
                :content="this.copys"
                style="width:5%;color:grey;font-size:100%;">
                <i slot="reference" style="display:inline-block;height:20px;font-size:130%;vertical-align:top" class="el-icon-share"></i>
            </el-popover>
        </h3>
        <div class="authOption">
            <p class="authTitle"><i class="el-icon-position"></i>&nbsp;浏览量</p>
            <p class="switches">{{haveviews}}</p>
        </div>
        <div class="authOption">
            <p class="authTitle"><i class="el-icon-s-order"></i>&nbsp;修改次数</p>
            <p class="switches">{{havemodifys}}</p>
        </div>
        <div class="authOption">
            <p class="authTitle"><i class="el-icon-chat-line-round"></i>&nbsp;评论量</p>
            <p class="switches">{{havecomments}}</p>
        </div>
    </div>
</template>

<script>
import QS from "qs";
const clickoutside = {
    update() {},
};
export default {
    props:{
      aid:Number,
    },
    name:'WeightList2',
    data(){
        return{
            md5:"",
            copys:"无",
            haveviews:0,
            havemodifys:0,
            havecomments:0,
        }
    },
    directives: {clickoutside},
    mounted: function() {
        this.getAllNum(this.aid);
        this.AIDgetUID()
    },
    methods: {
        /*get md5*/
        AIDgetUID(){
            var AID=parseInt(this.aid);
            let param_data = new FormData();
            param_data.append("AID",AID);
            this.$axios
                .post("/api/AIDtoUID/", param_data)
                .then(response => {
                    //this.authorUID=response.data.UID
                    this.md5=response.data.md5
                    this.copys="http://119.45.200.4/#/sharepage/"+this.md5;
                    console.log(response.data)
                }
                )
                .catch(error => {
                  this.$message({
                    message: "",
                    type: "error",
                    customClass: "c-msg",
                    duration: 0,
                    showClose: true,
                    duration: 1000
                  });
                  Promise.reject(error);
                });
        },
        getAllNum(article_id){
            let form = new FormData();
            form.append("AID",article_id);
            this.$axios.post("/api/getcheckNum/",form)
            .then(response=>{
                if(response.data.feedback){//解析成功
                    this.haveviews=parseInt(response.data.checknum);
                    this.havemodifys=parseInt(response.data.editnum);
                    this.havecomments=parseInt(response.data.commentnum);
                    console.log(response.data);
                }
                else{
                    this.$message({
                        message:"文章不存在或已删除!",
                        showClose:true,
                        duration:1000,
                        type:"error"
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"服务器好像出了点问题",
                    showClose:true,
                    duration:1000,
                    type:"error"
                })
            })
        },
        change: function(callback){
            console.log(callback);
        },
    }    
}
</script>
<style scoped>
.authTitle{
	width:40%;
	font-weight:bold;
	text-align:left;
	display:inline-block;
	font-size:100%;
	margin:auto auto auto auto;
}
.switches{
    width:55%;
    display:inline-block;
    text-align:right;
    margin:auto auto auto auto;
}
.authOption{
    margin-top:1ex;
}
</style>



