<template>
    <div class="mianStyle">
        <div style="padding:0px;margin-bottom:1%;margin-top:10%">
            <i class="el-icon-arrow-left" style="margin-right:1%;cursor:pointer" @click="toCraft"></i><span style="margin-right:0%">{{team.name}}</span>
        </div>
        <div>
            <el-menu :default-active="$route.path" background-color="#f7f7f7" router>
                <el-submenu index="1">
                    <template slot="title">
                        <i class="el-icon-folder-opened"></i>团队文档
                    </template>
                    <el-menu-item v-for="document in this.documents" :key="document.name" :index="'/Edit/'+uid+'/'+document.AID+'/-1/'+team.TID" style="padding-left:40px">
                            <div><span style="margin-right:5%;"><i class="el-icon-document"></i></span>{{document.documentName}}</div>
                    </el-menu-item>
                </el-submenu>
                <el-menu-item :index="'/teamRecycleBin/'+team.TID">
                    <template slot="title">
                        <i class="el-icon-delete"></i>团队回收站
                    </template>
                </el-menu-item>
            </el-menu>
        </div>
    </div>
</template>

<script>
export default {
    props:['documents','team'],
    data(){
        return{
            uid:this.$store.state.userProfile.UID,
        }
    },
    methods:{
        toArticle(AID,MID){
            this.$router.push({path:"/Edit/"+this.uid+"/"+AID+"/-1/"+this.team.TID});
        },
        toCraft(){
            this.$router.push({path:"/craftTable/"+this.uid});
        }
    }
}
</script>

<style>
.mainStyle{
    width:100%;
}
.el-submenu .el-menu-item{
    min-width:191px !important;
}
</style>


