<template>
    <div class="mainStyle">
        <el-button plain class="button" @click="toNew"><i class="el-icon-edit"></i>&nbsp;新建文档</el-button><br>
        <el-popover placement="bottom" trigger="hover" ref="models" popper-class="selector">
            <!--如果有模板-->
            <div v-if="this.documents.length!=0">
                <h3 style="text-align:center;margin:auto auto auto auto;padding:0.5ex 0ex 0.5ex 0ex">团队模板</h3>
                <div v-for="document in this.documents" :key="document.documentname" class="model">
                    <div style="cursor:pointer" @click="toNew1(document.MID)">
                        <el-card class="box-card" shadow="never" :body-style="{padding:'5px',}">
                            <div><span style="margin-right:5%"><i class="el-icon-document"></i></span>{{document.documentName}}</div>
                        </el-card>
                    </div>
                </div>
            </div>
            <!--如果没有-->
            <div v-else>
                <p style="text-align:center">当前没有团队模板</p>
            </div>
        </el-popover>
        <el-button plain class="button" v-popover="'models'"><i class="el-icon-edit-outline"></i>&nbsp;模板新建</el-button><br>
        <el-button v-if="showUpload" plain class="button" @click="goUpload()"><i class="el-icon-upload2"></i>&nbsp;导入文档</el-button>
		<input v-if="showUpload" type="file" style="display:none" ref="uploader" id="uploader" @change="uploadFile()"/>
        <el-select v-if="tagSelectionVisible" :placeholder="'请选择感兴趣的标签'" :clearable="true" style="width:70%;margin-top:2ex" v-model="selected" @change="throwTag()">
			<el-option
				v-for="(tag,index) in allTags"
				:key="index"
				:label="tag.name"
				:value="tag.name"
			>

			</el-option>
		</el-select>

    </div>
</template>

<script>
export default {
    name:"RightSliderTeam",
    props:['documents','TID','tagSelectionVisible','showUpload'],
    data(){
        return{
            isActive:false,
            uid:this.$store.state.userProfile.UID,
            allTags:[],
            selected:""
        }
    },
    created(){
        this.getAllTags();
    },
    methods:{
        /*从后端获取全部的标签 */
        getAllTags(){
            let form = new FormData();
            form.append("Mode","Team");
            form.append("UID",this.uid);
            form.append("TID",this.$route.params.TID);
            this.$axios.post("/api/getTags/",form)
            .then(response=>{
                if(response.data.Status){
                    this.allTags=response.data.tags;
                }
            })
            .catch(error=>{

            })
        },
        toNew(){
           this.$router.push({path:"/Edit/"+this.uid+"/-1"+"/-1/"+this.TID});
        },
        toNew1(MID){
            this.$router.push({path:"/Edit/"+this.uid+"/-1/"+MID+"/"+this.TID});
        },
        changeStyle(){
            this.isActive=true;
        },
        resetStyle(){
            this.isActive=false;
        },
        /*点击按钮之后，弹出文件选择框 */
		goUpload(){
			this.$refs.uploader.click();
		},
		/*选中文件之后，建文件上传 */
		uploadFile(){
			var file = document.getElementById("uploader").files[0];
			let form = new FormData();
            form.append("UID",this.$store.state.userProfile.UID);
            form.append("TID",this.TID);
			form.append("file",file);
			this.$axios.post("/api/teamUpload/",form)
			.then(response=>{
				if(response.data.Status){
					this.$message({
						message:"文档导入成功",
						type:"success",
						duration:1000,
						showClose:true
					})
				}
			})
			.catch(error=>{
				this.$message({
					message:"文档上传失败",
					type:"error",
					duration:1000,
					showClose:true
				})
			})
        },
        /*选择一个标签之后执行的函数 */
		throwTag(){
			this.$emit('searchTag',this.selected);
		}
    }
}
</script>
<style lang="scss">
.selector{
    padding:0px 0px 0px 0px;
}
</style>
<style scoped>
.button{
    width:70%;
    margin-top:2ex;
}
.model{
	font-weight:normal;
}
.model:hover{
	font-weight: bold;
	background:#e8e8e8
}
</style>



