<template>
    <div class="mainStyle">
        <div>
            <button class="button" @click="toNew">新建</button>
        </div>

        <div>
            <el-popover placement="bottom" trigger="hover">
                <div v-for="document in this.documents" :key="document.name">
                    <div style="cursor:pointer" @click="toNew1(document.MID)">
                        <el-card class="box-card" shadow="never" :body-style="{padding:'5px',}">
                            <div><span style="margin-right:5%"><i class="el-icon-document"></i></span>{{document.name}}</div>
                        </el-card>
                    </div>
                </div>
                <el-button class="button" slot="reference">模板新建</el-button>
            </el-popover>
        </div>    
    </div>
</template>

<script>
export default {
    name:"WorkRight",
    props:['documents'],
    data(){
        return{
            isActive:false,
            uid:this.$store.state.userProfile.UID,
        }
    },
    methods:{
        toNew(){
           this.$router.push({path:"/Edit/"+this.uid+"/-1"+"/-1"});
        },
        toNew1(MID){
            this.$router.push({path:"/Edit/"+this.uid+"/-1"+MID});
        },
        changeStyle(){
            this.isActive=true;
        },
        resetStyle(){
            this.isActive=false;
        }
    }
}
</script>
<style scoped>
.button{
    height:30px;
    width:10%;
    margin:8px;
    padding:0;
    border:0;
    border-radius: 2px;
    display: block;
    outline:none;
    font-size:20px;
    font-family: 'Courier New', Courier, monospace;
    font-weight: normal;
    color:white;
    background:radial-gradient(rgb(65,70,75) 100%,rgb(60, 60, 60) 0%,rgb(0, 0, 0));
}
.button:active{
    background:#000
}
.active{
    background-color: #f7f7f7;
}
</style>


