<template>
    <div style="border:1px solid #e0e0e0;width:30%;border-radius:5px;background:#ffffff">
        <h1>欢迎登录</h1>
        <hr style="width:90%">
        <form
            id = "outer"
            style="text-align:center;padding-left:10%;padding-right:10%;padding-top:5px;padding-bottom:5px"
            @submit.prevent="FormSubmit()"
        >
            <!--输入用户名-->
            <div style="text-align:left;width:100%;padding:1ex">
                <label for="Username" style="text-align:left;font-size:120%">邮箱<span style="color:red">*</span></label><br>
                <el-input 
                    type="input" 
                    required="required" 
                    name="Username" 
                    id="Username" 
                    v-model="email" 
                    placeHolder="请输入邮箱" 
                    style="width:100%;font-size:120%;margin-top:0.5ex"
                />
                <br>
            </div>
            <!--输入密码-->
            <div style="text-align:left;width:100%;padding:1ex">
                <label for="password" style="text-align:left;font-size:120%">密码<span style="color:red">*</span></label><br>
                <el-input 
                    type="password" 
                    required="required" 
                    name="Password" 
                    placeholder="请输入密码" 
                    id="password" 
                    v-model="password" 
                    style="width:100%;font-size:120%;margin-top:0.5ex"
                    show-password
                /><br>
            </div>
            <el-button plain type="primary" class="button" style="margin-top:1.5ex;width:40%;margin-bottom:1.5ex" @click="FormSubmit()">登录</el-button>
        </form>
    </div>
</template>
<script>
export default {
    data(){
        return{
            email:"",
            password:""
        }
    },
    methods:{
        /*提交整个表单 */
        FormSubmit(){
            if(this.Email==""){
                this.$message({
                    message:"请您填写用户名/邮箱",
                    duration:1000,
                    showClose:true,
                    type:"error"
                })
                return;
            }
            else if(this.password==""){
                this.$message({
                    message:"请您填写密码",
                    duration:1000,
                    showClose:true,
                    type:"error"
                })
            }
            else{
                let form = new FormData();
                form.append("Email", this.email);
                form.append("Password", this.$md5(this.password));
                this.$axios.post("/api/login/",form)
                .then(response=>{
                    if(response.data.Status){//登录成功
                        this.$message({
                            message:"登陆成功，欢迎光临金刚石文档",
                            type:"success",
                            duration:1000,
                            showClose:true
                        })
                        this.$store.commit('changeUID',response.data.UserId);
                        console.log("UID changed!");
                        this.$store.commit('changeUserName',response.data.Username);
                        console.log("Username changed!");
                        if(response.data.AvatarUrl!=""){
                            this.$store.commit('changeAvatarUrl',"http://119.45.200.4/"+response.data.AvatarUrl);
                        }else{
                            this.$store.commit('changeAvatarUrl',response.data.AvatarUrl);
                            console.log("AvatarUrl为空")
                        }
                        console.log("AvatarUrl changed!");
                        this.$store.commit('changeUserState',true);
                        console.log("User State changed!");
                        console.log("ToRead的值"+sessionStorage.getItem("ToRead"))
                        if(window.sessionStorage.getItem("ToRead")==null||window.sessionStorage.getItem("ToRead")=="null"){
                            this.$router.push("/craftTable/"+this.$store.state.userProfile.UID)
                            sessionStorage.setItem("ToRead",null);
                            console.log(sessionStorage.getItem("ToRead"));
                        }
                        else{
                            this.$router.push("/sharepage/"+this.$store.state.userProfile.UID+"/"+sessionStorage.getItem("ToRead"))
                            sessionStorage.setItem("ToRead",null);
                            console.log(sessionStorage.getItem("ToRead"))
                        }
                    }
                    else{//登陆失败
                        this.$message({
                            message:"登陆失败，邮箱或密码有误",
                            type:"error",
                            duration:1000,
                            showClose:true
                        })
                    }
                })
                .catch(error=>{
                    this.$message({
                        message:"服务器好像有点问题",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
                })
            }
        },
    }
}
</script>
<style scoped>

</style>


