<template>
    <div>
        <div v-clickoutside="hideReplyBtn" @click="inputFocus" class="my-reply">
            <!--发送评论-->
            <div class="reply-info">
                <div 
                tabindex="0" 
                :contenteditable="this.myuid!=null&&this.level>=this.commentlevel" 
                id="replyInput" 
                spellcheck="false" 
                :placeholder="this.myuid!=null&&this.level>=this.commentlevel?'输入评论...':'请先登录 / 你没有权限进行评论'" 
                class="reply-input" 
                @input="onDivInput($event)"
                >
                </div>
            </div>
            <!--发送评论的按钮-->
            <div class="reply-btn-box" v-if="myuid!=null">
                <el-button class="reply-btn" size="medium" @click="sendComment" type="primary" >发表评论</el-button>
            </div>
        </div>
        <!--展示评论-->
        <div v-for="(item,i) in comments" :key="i" class="author-title reply-father" style="text-align:left">
            <div class="icon-btns" v-if="item.cid==tcid">
                <i style="color:red;" class="el-icon-collection-tag"></i>
                <span style="color:red;font-size:15px;vertical-align:top;">置顶</span>
            </div>
            <!--评论拥有者信息-->
            <div class="author-info">
                <el-avatar style="width:25px ;height:25px;vertical-align:top;" :src="item.imageurl!=''?'http://119.45.200.4/'+item.imageurl:src_logo"></el-avatar>
                <span class="author-name" @click="toProfile(item.uid)">{{item.name}}</span>
                <span class="author-time">{{item.time}}</span>
            </div>
            <!--评论内容展示-->
            <div class="talk-box" style="text-align:left">
                <p style="text-align:left;margin-left:5px">
                    <span class="reply" v-html="item.content" style="text-align:left"></span>
                </p>
            </div>
            <!--回复评论、删除评论和置顶评论-->
            <div class="icon-btn" style="margin-bottom:15px;padding-bottom:5px"  v-if="myuid!=null">
                <i @click="showReplyInput(i,item.name,item.cid)" v-if="level>=commentlevel||myuid==authorUID" style="display:inline-block;vertical-align:middle" class="iconfont el-icon-s-comment"></i>
                <i @click="deleteComment(item.cid)" v-if="(myuid==item.uid&&level>=commentlevel)||myuid==authorUID" style="display:inline-block;vertical-align:middle" class="iconfont el-icon-delete"></i>
                <i @click="getToTop(item.cid)" v-if="myuid==authorUID" style="display:inline-block;vertical-align:middle" class="iconfont el-icon-s-flag"></i>
            </div>
            <br>
            <br>
            <!--评论回复区域-->
            <div class="reply-box" >
                <div v-for="(replys,j) in item.reply" :key="j" class="author-title">
                    <!--评论发送者信息-->
                    <div class="author-info">
                        <el-avatar style="width:25px ;height:25px;vertical-align:top;" :src="replys.imageurl!=''?'http://119.45.200.4/'+replys.imageurl:src_logo"></el-avatar>
                        <span class="author-name" @click="toProfile(replys.uid)">{{replys.name}}</span>
                        <span class="author-time">{{replys.time}}</span>
                    </div>
                    <!--回复文本-->
                    <div class="talk-box" style="text-align:left">
                        <p style="text-align:left;margin-left:5px">
                            <span>回复 {{replys.to}}:</span>
                            <span class="reply" v-html="replys.content"></span>
                        </p>
                    </div>
                    <!--删除按钮-->
                    <div class="icon-btn" v-if="myuid!=null&&((myuid==replys.uid&&level>=commentlevel)||myuid==authorUID)" >
                        <i @click="deleteReplyComment(replys.cid,i)" style="display:inline-block;vertical-align:middle" class="iconfont el-icon-delete"></i>
                    </div>
                    <div class="reply-box">
                    </div>
                    <br>
                </div>
            </div>
            <!--评论回复框-->
            <div  v-show="_inputShow(i)" class="my-reply my-comment-reply">
                <div class="reply-info" >
                    <div tabindex="0" contenteditable="true" spellcheck="false" placeholder="输入评论..." @input="onDivInput($event)"  class="reply-input reply-comment-input">

                    </div>
                </div>
                <div class=" reply-btn-box">
                    <el-button class="reply-btn" size="medium" @click="sendCommentReply(i)" type="primary">
                        发表评论
                    </el-button>
            </div>
        </div>
        </div>
    </div>
</template>
<script>
import Personalnavigation from "@/components/Comment/Personalnavigation.vue";
import QS from "qs";
import icon_logo from "@/static/icon_profile.png"

const clickoutside = {
    // 初始化指令
    bind(el, binding, vnode) {
    function documentHandler(e) {
    // 这里判断点击的元素是否是本身，是本身，则返回
        if (el.contains(e.target)) {
            return false;
        }
    // 判断指令中是否绑定了函数
        if (binding.expression) {
            // 如果绑定了函数 则调用那个函数，此处binding.value就是handleClose方法
            binding.value(e);
        }
    }
    // 给当前元素绑定个私有变量，方便在unbind中可以解除事件监听
    el.vueClickOutside = documentHandler;
    document.addEventListener('click', documentHandler);
    },
    update() {},
    unbind(el, binding) {
    // 解除事件监听
    document.removeEventListener('click', el.vueClickOutside);
    delete el.vueClickOutside;
  },
};
export default {
    props: {
        AID: String,
        md5: String,
        myuid:String,
        level:Number,
        commentlevel:Number
    },
    name:'Comment',
    components: {
        Personalnavigation
    },
    data(){
        return{
            src_logo:icon_logo,
            /*AID: "2",
            myuid:"2",
            level:5,
            commentlevel:1,*/
            num:0,
            authorUID:"String",
            myname: "String",
            myemail: "String",
            myimgurl:"String",
            btnShow: false,
            index:'0',
            replyComment:'',
            to:'',
            toId:-1,
            comments:[],
            tcid:"8"
        }
    },
    directives: {clickoutside},
    mounted: function() {
        if(this.AID==""){
            this.Md5getAID()
        }
        else{
            this.getComment();
            //this.getToTop("-1")
            this.AIDgetUID()
        }
        console.log("cur_uid:"+this.myuid);
        var param_data = {user_id: this.myuid};
        if(this.myuid!=null&&this.level>=this.commentlevel){
            this.$axios.post("/api/PersonIndex/", QS.stringify(param_data))
                .then(response => {
                  // post 成功，response.data 为返bai回的数据
                    this.myname=response.data.Username;
                    this.myemail=response.data.Email;
                    this.myimgurl=response.data.imageUrl;
                    console.log(this.myimgurl)
                    /*console.log(response.data.email)
                    console.log(response.data.mobile)
                    console.log(this.form.need_name)*/
                })
                .catch(error => {
                  // 请求失败du
                  console.log(error)
             })
        }
        console.log("comments:"+this.comments);
    },
    methods: {
        Md5getAID(){
            let form = new FormData();
            form.append("md5",this.md5);
            this.$axios.post("/api/AIDgetMD5/",form)
            .then(response=>{
                if(response.data.feedback){//解析成功
                    this.AID=response.data.AID;
                    this.getComment();
                    //this.getToTop("-1")
                    this.AIDgetUID();
                }
                else{
                    this.$message({
                        message:"文章不存在或已删除~",
                        showClose:true,
                        duration:1000,
                        type:"error"
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"服务器好像出了点问题",
                    showClose:true,
                    duration:1000,
                    type:"error"
                })
            })
        },
        AIDgetUID(){
            var aid=parseInt(this.AID);
            var param_data = {AID: aid};
            this.$axios
                .post("/api/AIDtoUID/", QS.stringify(param_data))
                .then(response => {
                    this.authorUID=response.data.UID
                    //this.md5=response.data.md5
                    console.log(response.data)
                }
                )
                .catch(error => {
                  this.$message({
                    message: "",
                    type: "error",
                    customClass: "c-msg",
                    duration: 0,
                    showClose: true,
                    duration: 1000
                  });
                  Promise.reject(error);
                });
        },
        getIndexOf(val,arr) {
            for (var i = 0; i < arr.length; i++) {
                if (arr[i].cid == val) return i;
            }
            return -1;
        },
        Remove(val,arr) {
            var index = this.getIndexOf(val,arr);
            if (index > -1) {
                arr.splice(index, 1);
            }
        },
        getToTop(cid){
            var aid=parseInt(this.AID);
            var tcids=parseInt(cid);
            if(cid==this.tcid){
                tcids="-1";
            }
            var param_data = {AID: aid,TCID: tcids};
            this.$axios
                .post("/api/gototop/", QS.stringify(param_data))
                .then(response => {
                    console.log(response.data)
                    if(tcids=="-1"){
                        this.$message({
                            message:
                            "成功取消置顶！",
                            type: "success",
                            customClass: "c-msg"
                        });
                    }
                    else{
                        this.$message({
                            message:
                            "成功设置置顶！",
                            type: "success",
                            customClass: "c-msg"
                        });
                    }
                    this.getComment();
                }
                )
                .catch(error => {
                  this.$message({
                    message: "",
                    type: "error",
                    customClass: "c-msg",
                    duration: 0,
                    showClose: true,
                    duration: 1000
                  });
                  Promise.reject(error);
                });
        },
        getComment(){
            this.comments=[];
            var aid=parseInt(this.AID);
            var param_data = {AID: aid};
            this.$axios
                .post("/api/CommentList/", QS.stringify(param_data))
                .then(response => {
                    this.tcid=response.data.tcid;
                    if(this.tcid!="-1"){
                        var pos=this.getIndexOf(this.tcid,response.data.comments);
                        for (var i = 0; i < response.data.comments.length; i++) {
                            if(i!=pos)this.comments.unshift(response.data.comments[i]);
                        }
                        this.comments.unshift(response.data.comments[pos]);
                        console.log(this.comments)
                    }
                    else{
                        this.comments=response.data.comments;
                    }
                    console.log(response.data)
                }
                )
                .catch(error => {
                  this.$message({
                    message: "",
                    type: "error",
                    customClass: "c-msg",
                    duration: 0,
                    showClose: true,
                    duration: 1000
                  });
                  Promise.reject(error);
                });
        },
        toProfile(id){
          this.$message({
            message:
              "跳转成功，\\(^o^)/~。",
            type: "success",
            customClass: "c-msg"
          });
          this.$router.push({ 
            path : "/OtherIndex/"+id
          });
        },
        inputFocus(){
            var replyInput = document.getElementById('replyInput');
            replyInput.style.padding= "8px 8px"
            replyInput.style.border ="2px solid blue"
            replyInput.focus()
        },  
        showReplyBtn(){
            this.btnShow = true
        },
        hideReplyBtn(){
            this.btnShow = false
            replyInput.style.padding= "10px"
            replyInput.style.border ="none"
        },
        showReplyInput(i,name,id){
            this.comments[this.index].inputShow = false
            console.log(this.index);
            this.index =i
            console.log(this.index);
            this.comments[i].inputShow = true
            console.log(this.comments);
            this.to = name
            this.toId = id
        },
        _inputShow(i){
            return this.comments[i].inputShow&&this.myuid!=null&&this.level>=this.commentlevel
        },
        deleteComment(cid){
            var pos=this.getIndexOf(cid,this.comments)
            console.log(pos);
            for(var i = 0; i < this.comments[pos].reply.length; i++) {
                this.deleteReplyComment(this.comments[pos].reply[i].cid,pos)
            }
            if(cid==this.tcid){
                this.tcid="-1";
                this.getToTop(this.tcid);
            }
            this.Remove(cid,this.comments)
            var param_data = {};
            param_data.CID=cid;
            console.log(param_data);
            this.$axios
                .post("/api/DeleteComment/", QS.stringify(param_data))
                .then(response => {
                    console.log(response.data);
                    this.$message({
                            message:
                            "成功删除评论！",
                            type: "success",
                            customClass: "c-msg"
                        });
                })
                .catch(error => {
                    this.$message({
                    message: "ssfd",
                    type: "error",
                    customClass: "c-msg",
                    duration: 0,
                    showClose: true
                    });
                    Promise.reject(error);
                });
        },
        deleteReplyComment(cid,i){
            this.Remove(cid,this.comments[i].reply)
            var param_data = {CID : cid};
            console.log(param_data);
            this.$axios
                .post("/api/DeleteComment/", QS.stringify(param_data))
                .then(response => {
                    console.log(response.data);
                    this.$message({
                            message:
                            "成功删除评论！",
                            type: "success",
                            customClass: "c-msg"
                        });
                })
                .catch(error => {
                    this.$message({
                    message: "ssfd",
                    type: "error",
                    customClass: "c-msg",
                    duration: 0,
                    showClose: true
                    });
                    Promise.reject(error);
                });
        },
        sendComment(){
            if(!this.replyComment){
                 this.$message({
                    showClose: true,
                    type:'warning',
                    message:'评论不能为空'
                })
            }else{
                let a ={}
                let input =  document.getElementById('replyInput')
                let time= this.dateStr();
                a.name= this.myname
                a.email=this.myemail
                a.uid=this.myuid
                a.content =this.replyComment
                a.inputShow = false
                a.time = time
                a.imageurl=this.myimgurl
                a.reply=[]
                this.comments.unshift(a)
                this.replyComment = ''
                input.innerHTML = '';
                var param_data = {
                    AID: this.AID,
                    UID: a.uid,
                    content: a.content,
                };
                console.log(param_data);
                this.$axios
                    .post("/api/NewComment/", QS.stringify(param_data))
                    .then(response => {
                      a.cid=response.data.CID;
                    })
                    .catch(error => {
                      this.$message({
                        message: "",
                        type: "error",
                        customClass: "c-msg",
                        duration: 0,
                        showClose: true
                      });
                      Promise.reject(error);
                    });
            }
        },
        sendCommentReply(i){
            if(!this.replyComment){
                this.$message({
                    showClose: true,
                    type:'warning',
                    message:'评论不能为空'
                })
            }else{
                let a ={}
                let time= this.dateStr();
                a.name= this.myname
                a.email=this.myemail
                a.uid=this.myuid
                a.to = this.to
                a.inputShow = false
                a.content =this.replyComment
                a.imageurl=this.myimgurl
                a.time = time
                this.comments[i].reply.unshift(a)
                this.replyComment = ''
                document.getElementsByClassName("reply-comment-input")[i].innerHTML = ""
                let rpid=this.comments[i].cid
                console.log(this.comments[i].cid)
                var param_data = {
                    RPID: rpid,
                    comment: {
                        AID: this.AID,
                        UID: a.uid,
                        content: a.content,
                    }
                };
                console.log(param_data);
                this.$axios
                    .post("/api/ReplyComment/", param_data)
                    .then(response => {
                      a.cid=response.data;
                    })
                    .catch(error => {
                      this.$message({
                        message: "",
                        type: "error",
                        customClass: "c-msg",
                        duration: 0,
                        showClose: true
                      });
                      Promise.reject(error);
                    });
            }
        },
        onDivInput: function(e) {
            this.replyComment = e.target.innerHTML;
        },
        dateStr(){
            var dt = new Date();
            return (dt.getFullYear() + '-' + (dt.getMonth() + 1) + '-' + dt.getDate() + ' ' + dt.getHours() + ':' + dt.getMinutes() + ':' + dt.getSeconds()).replace(/([\-\: ])(\d{1})(?!\d)/g, '$10$2');
        }
    }    
}
</script>
<style lang="stylus" scoped>
.option:hover{
    background:#f0f0f0;
    font-weight:bold
}
.option:active{
    background:#e8e8e8;
    font-weight:bold;
}
.my-reply
    padding 10px
    background-color #fafbfc
    .reply-info    
        display inline-block
        margin-left 5px
        width 90%
        @media screen and (max-width:1200px) {
            width 80%
        }
        .reply-input
            min-height 20px
            line-height 22px
            padding 10px 10px
            color #888
            background-color #fff
            border-radius 5px
            &:empty:before
                content attr(placeholder)
            &:focus:before
                content none
            &:focus
                padding 8px 8px
                border 2px solid blue
                box-shadow none
                outline none
    .reply-btn-box
        height 25px
        margin 10px 0
        .reply-btn
            position relative
            float right
            margin-right 15px
.my-comment-reply
    margin-left 50px
    .reply-input
        width flex
.author-title:not(:last-child)
    border-bottom: 1px solid rgba(178,186,194,.3)
.author-title
    padding 10px
    .author-info
      display inline-block
      text-align: left
      margin-left 5px
      width 100%
      height 50px
      line-height 25px
      >span 
        display inline-block
        cursor pointer
        overflow hidden
        white-space nowrap
        text-overflow ellipsis
      .author-name
        color #000
        font-size 20px
        font-weight bold
      .author-info2
        display block
        text-align: left
        margin-left 5px
        width 60%
        height 20px
        line-height 15px
        >span 
          display inline-block
          cursor pointer
          overflow hidden
          white-space nowrap
          text-overflow ellipsis
        .author-uid
          color #999
          font-size 8px
        .author-email
          font-size 8px
        .author-mobile
          font-size 8px
      .author-time
        text-align: right
        font-size 14px
    .icon-btns
        font-size:125%
        width 15%
        padding 0 !important 
        float right
        @media screen and (max-width : 1200px){
            width 20%
            padding 7px
        }
    .icon-btn
        cursor pointer
        width 30%
        padding 0 !important 
        float right
        @media screen and (max-width : 1200px){
            width 20%
            padding 7px
        }
        >span 
            cursor pointer
        .iconfont 
            width 10px
            height 10px
            margin 0 5px
        .iconfonts 
            width 20px
            height 20px
            margin 0 5px
    .talk-box
        >p
           margin 0
        .reply
            font-size 16px
            color #000
    .reply-box
        margin 10px 0 0 50px
        background-color #efefef
</style>



