<template>
    <!--总体-->
    <div>
        <!--中间的编辑器部分-->
        <div style="display:inline-block;width:75%;vertical-align:top">
            <!--输入题目-->
            <input
                type="text"
                style="width:95%;
                    border:0px;
                    border-bottom:1px solid #aaaaaa;
                    font-size:200%;
                    margin-top:5px;
                    text-align:center;
                    padding:5px;
                    "
                placeholder="请输入文档题目"
                v-model="articleInfo.title"
                :disabled="!this.Editable"
            />
            <!--输入内容-->
            <mavon-editor
                style="width:99%;margin-top:10px;box-shadow:0px 0px 0px #fff;height:90ex"
                v-model="articleInfo.content"
                :subfield="this.Editable"
                :defaultOpen="'preview'"
                ref="editor"
                @imgAdd="$imgAdd"
                :toolbarsFlag="this.Editable"
            />
        </div>
        <!--右边的按钮区-->
        <div style="display:inline-block;text-align:center;width:20%;vertical-align:top">
            <div style="width:100%;margin-top:2ex">
                <!--保存文档选项-->
                <el-button plain style="width:80%" @click="saveAsMine('doc')" :disabled="!this.Editable">
                    保存
                </el-button>
                <br>
            </div>
            <div v-if="aid!=-1" style="width:100%;margin-top:2ex">
                <!--文章基本数据-->
                <div>
                    <h3 style="padding:1ex;margin:auto auto auto auto;border-bottom:1px solid #e8e8e8">文章信息</h3>
                    <div class="authOption">
                        <p class="authTitle"><i class="el-icon-position"></i>&nbsp;浏览量</p>
                        <p class="switches">{{haveviews}}</p>
                    </div>
                    <div class="authOption">
                        <p class="authTitle"><i class="el-icon-s-order"></i>&nbsp;修改次数</p>
                        <p class="switches">{{havemodifys}}</p>
                    </div>
                    <div class="authOption">
                        <p class="authTitle"><i class="el-icon-chat-line-round"></i>&nbsp;评论量</p>
                        <p class="switches">{{havecomments}}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import viewNumSider from "@/components/viewNumSider/viewNumSider.vue";
import WeightList from "@/components/WeightList/WeightList.vue"
import WeightList2 from "@/components/WeightList2/WeightList2.vue"
export default {
    components:{
        WeightList,
        WeightList2,
        viewNumSider
    },
    props:{
        Editable:Boolean,//是否有权利修改
        MyLevel:Number,//我的等级
        isRead:Boolean
    },
    data(){
        return{
            haveviews:0,
            havemodifys:0,
            havecomments:0,
            md5:String,
            aid:Number,//文章id
            uid:Number,//用户id
            tid:Number,//团队id
            mid:Number,//模板的id
            articleInfo:{//文章信息
                content:"",//文章的内容
                title:"",//题目的内容
            },
            Tags:[],
            inputVisible:false,
            inputValue:{
                name:'',
                type:''
            },
            checkVisible:1,
            noTag:true
        }
    },
    /*刚刚创建的时候 */
    created(){
        this.tid=parseInt(this.$route.params.TID==undefined?"-1":this.$route.params.TID);
        //this.aid=parseInt(this.$route.params.AID);
        this.md5=this.$route.params.AID;
        this.uid=parseInt(this.$route.params.UID);
        this.mid=parseInt(this.$route.params.MID);
        //this.MD5getAID();
        /*判断模板相关 */
        if((!isNaN(this.uid))&&this.uid!=null){
            this.MD5getAID()
        }
    },
    methods:{
        getVisitAuthority(){
            let form = new FormData();
            form.append("AID",this.aid);
            form.append("UID",this.uid);
            this.$axios.post("/api/authJudger/",form)
            .then(response=>{
                if(response.data.Status){//解析成功
                    this.PrivateOwn=response.data.PrivateOwn//这篇文章是不是这个用户私有？（且不属于任何团队）如果是，后面的属性无意义
                    this.TeamOwn=response.data.TeamOwn//这篇文章是不是团队所有，且用户属于这个团队？如果是，后面的属性无意义
                    this.CanRead=response.data.CanRead//这篇文章用户能不能阅读
                    this.CanComment=response.data.CanComment//这篇文章用户能否评论
                    this.CanEdit=response.data.CanEdit
                    console.log(this.TeamOwn)
                    if(this.CanRead){//不可阅读，返回工作台
                        console.log("啦啦啦")
                        this.getArticle(this.aid);
                    }
                }
                else{
                    this.$message({
                        message:"该文章没有设置权限",
                        showClose:true,
                        duration:1000,
                        type:"error"
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"服务器好像出了点问题1111",
                    showClose:true,
                    duration:1000,
                    type:"error"
                })
            })
        },
        getAllNum(article_id){
            let form = new FormData();
            form.append("AID",article_id);
            this.$axios.post("/api/getcheckNum/",form)
            .then(response=>{
                if(response.data.feedback){//解析成功
                    this.haveviews=parseInt(response.data.checknum);
                    this.havemodifys=parseInt(response.data.editnum);
                    this.havecomments=parseInt(response.data.commentnum);
                    console.log(response.data);
                }
                else{
                    this.$message({
                        message:"文章不存在或已删除",
                        showClose:true,
                        duration:1000,
                        type:"error"
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"服务器好像出了点问题",
                    showClose:true,
                    duration:1000,
                    type:"error"
                })
            })
        },
        /*从后端获取文章/模板信息 */
        MD5getAID(){
            let form = new FormData();
            form.append("md5",this.md5);
            this.$axios.post("/api/AIDgetMD5/",form)
            .then(response=>{
                if(response.data.feedback){//解析成功
                    this.aid=response.data.AID;
                    if(this.aid!=-1){//阅读已有的文章，或者根据模板新建
                        console.log("不是新建");
                        this.getAllNum(this.aid)
                        this.getVisitAuthority();
                    }
                    else if(this.mid!=-1){
                        console.log("模板新建");
                        this.getArticle(this.mid);
                    }
                }
                else{
                    this.$message({
                        message:"文章不存在或已删除",
                        showClose:true,
                        duration:1000,
                        type:"error"
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"服务器好像出了点问题",
                    showClose:true,
                    duration:1000,
                    type:"error"
                })
            })
        },
        getArticle(article_id){
            let form = new FormData();
            form.append("AID",article_id);
            form.append("UID",this.uid);
            this.$axios.post("/api/getArticle/",form)
            .then(response=>{
                if(response.data.Status){//解析成功
                    this.articleInfo.content=response.data.Content;
                    this.articleInfo.title=response.data.Title;
                }
                else{
                    this.$message({
                        message:"文章不存在或已删除",
                        showClose:true,
                        duration:1000,
                        type:"error"
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"服务器好像出了点问题",
                    showClose:true,
                    duration:1000,
                    type:"error"
                })
            })
        },
        /*保存文档，考虑以下几种问题 */
        //创建个人文档
        //更新团队文档
        //创建个人文档
        //更新团队文档
        saveAsMine(type){
            let targetUrl = (type=='doc'?"/api/sendMyArticle/":"/api/sendMyModel/")
            if(type=='model'){//新建模板
                this.aid=-1;
            }
            /*let form = new FormData();
            form.append("UID",this.uid);
            form.append("AID",this.aid);
            form.append("Tags",this.Tags);
            if(this.articleInfo.content!="")form.append("Content",this.articleInfo.content);
            else form.append("Content","暂时还没内容");
            if(this.articleInfo.title!="")form.append("Title",this.articleInfo.title);
            else form.append("Title","暂时还没想好题目");
            form.append("TID",(this.tid=="-1"?"":this.tid));*/
            let T=[];
            for(var i=0;i<this.Tags.length;i++){
                var obj=new Object();
                obj.name=this.Tags[i].name;
                obj.type=this.Tags[i].type;
                T.push(obj);
            }
            if(this.articleInfo.content=="")
                this.articleInfo.content="暂时还没内容";
            if(this.articleInfo.title=="")
                this.articleInfo.title="暂时还没想好题目";
            let form={};
            form={
                TID:(this.tid=="-1"?"":this.tid),
                UID:this.uid,
                AID:this.aid,
                Content:this.articleInfo.content,
                Title:this.articleInfo.title,
                Tags:T
            }
            this.$axios.post(targetUrl,form)
            .then(response=>{
                if(response.data.Status){//发送成功
                    this.$message({
                        message:"保存成功",
                        duration:1000,
                        showClose:true,
                        type:"success"
                    })
                }
                else{
                    this.$message({
                        message:"服务器忙，请待会再试",
                        duration:1000,
                        showClose:true,
                        type:"error"
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"服务器好像有点问题",
                    duration:1000,
                    showClose:true,
                    type:"error"
                })
            })
        },
        $imgAdd(pos, $file){
            let form = new FormData();
            form.append('img', $file);
            this.$axios.post("/api/picSave/",form,{
                headers:{
                    'Content-Type': 'multipart/form-data'
                }
            }).then(response=>{
                this.$refs.editor.$img2Url(pos,"http://119.45.200.4"+response.data.imgUrl);
            })
        },
    }
}
</script>
<style scoped>
.authTitle{
	width:40%;
	font-weight:bold;
	text-align:left;
	display:inline-block;
	font-size:100%;
	margin:auto auto auto auto;
}
.switches{
    width:55%;
    display:inline-block;
    text-align:right;
    margin:auto auto auto auto;
}
.authOption{
    margin-top:1ex;
}
</style>




