<template>
  <div class="all" style="text-align: left;height: 100%;width: 10%;">
    <div id="1" style="margin-top: 20%;cursor:pointer" @click="go1">
      <i class="el-icon-box" style="font-size: 120%"></i>
      <span style="margin-left: 5%;font-size: 120%;color: darkgray;margin-top: 5%">{{ first1 }}</span>
    </div>
    <el-divider></el-divider>
    <div id="2" style="margin-top: 10%;cursor:pointer" @click="go2">
      <i class="el-icon-folder" style="font-size: 120%"></i>
      <span style="margin-left: 5%;font-size: 120%;color: darkgray">{{ second1 }}</span>
    </div>
      <div id="3" style="margin-top: 15%;cursor:pointer;">
        <i class="el-icon-s-home" style="font-size: 120%"></i>
        <span style="margin-left: 5%;font-size: 120%;color: darkgray" @click="change">{{ third1 }}</span>
        <i class="el-icon-caret-bottom" v-if="isFolded"></i>
        <i class="el-icon-caret-top" v-if="!isFolded"></i>
        <i class="el-icon-circle-plus" @click="digitalFromVisible=true"></i>
        <el-dialog title="新建团队空间" :visible.sync="digitalFromVisible">
          <el-form :model="form" :rules="rules" ref="forms">
            <el-form-item label="空间名称" label-width="20%" prop="teamName">
              <el-input v-model="form.teamName" auto-complete="false" :validate-event="true"></el-input>
            </el-form-item>
          </el-form >
          <div slot="footer" class="dialog-footer">
            <el-button @click="digitalFromVisible = false">取 消</el-button>
            <el-button type="primary" @click="FormSubmit">确 定</el-button>
          </div>
        </el-dialog>
        <div v-if="!isFolded" style=";cursor:pointer">
          <div v-for="team in this.team" @click="go3(team.TID)">
            <span style="margin-left: 5%;font-size: 120%;color: black">-</span>
            <span style="margin-left: 10%;font-size: 120%;color: black">{{ team.teamName }}</span>
          </div>
        </div>
      </div>
    <el-divider></el-divider>
    <div id="4" style="margin-top: 20%;cursor:pointer" @click="go4">
      <i class="el-icon-milk-tea" style="font-size: 120%"></i>
      <span style="margin-left: 5%;font-size: 120%;color: darkgray">{{ forth1 }}</span>
    </div>
    <br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br>
  </div>
</template>

<script>
  import QS from "qs";
    export default {
        name: "LeftSider",
      //需要传入当前用户的团队和当前用户的id
        props:["team"],
        data(){
          return{
            first1:"工作台",
            second1:"我的文档",
            third1:"我的团队",
            forth1:"回收站",
            isFolded:true,
            digitalFromVisible:false,
            form: {
              teamName: '',
              teamOwner:this.$store.state.userProfile.UID
            },
            rules:{
              teamName:[
                {
                  validator:this.validateTeamName,
                  trigger: 'blur'
                },
                {
                  required:true,
                  message:"请填写团队名字",
                  trigger:'blur'
                }
              ]
            }
          }
        },
      methods:{
          change(){
            this.isFolded = !this.isFolded;
          },
          go1() {
            this.$router.push("/craftTable/" + this.$store.state.userProfile.UID);
          },
          go2(){
            this.$router.push("/myDocs/"+this.$store.state.userProfile.UID);
          },
          go3(TID){
            this.$router.push("/myTeam/"+this.$store.state.userProfile.UID+"/"+TID);
          },
          go4(){
            this.$router.push("/personalRecycleBin/"+this.$store.state.userProfile.UID);
          },
          onInput(){
            this.$forceUpdate();
          },
          validateTeamName(rule,value,callback){
              console.log("111");
              this.$axios
                .post("/api/checkTeamName/",QS.stringify(this.form))
                .then(response =>{
                    if(this.form.teamName!==""&&response.data.isExist===false){
                      callback(new Error("团队名称重复!"));
                    }else{
                      callback();
                    }
                }).catch( error =>{
                    this.$message({
                      message:"该页面出现了一些情况",
                      type:"error",
                      customClass:"c-msg",
                      duration:0,
                      showClose:true
                    });
                    Promise.reject(error);
              });
          },
          FormSubmit(){
              this.$refs.forms.validate(valid => {
                  if(valid){
                    this.digitalFromVisible=false;
                    let newForm =new FormData();
                    newForm.append("teamOwner",this.$store.state.userProfile.UID);
                    newForm.append("teamName",this.form.teamName);
                    this.$axios
                      .post("/api/checkTeam/",newForm)
                      .then(response => {
                        if(response.data.status){
                          this.form.teamName="",
                          this.team.push({teamName:response.data.teamName,TID:response.data.TID}) ;
                          this.$emit("changeTeam",{teamName:response.data.teamName,TID:response.data.TID});
                            this.$message({
                              message:"创建成功",
                              type:"success",
                              customClass:"c-msg"
                            })
                          this.team.push({
                            TID:response.data.TID,
                            name:response.data.teamName
                          });
                        }
                        else{
                          this.form.teamName="",
                            this.$message({
                              message:"创建失败",
                              type:"error",
                              customClass:"c-msg"
                            })
                        }
                      })
                        .catch(error =>{
                          this.$message({
                            message:"该页面出现了一些情况",
                            type:"error",
                            customClass:"c-msg",
                            duration:0,
                            showClose:true
                          });
                           Promise.reject(error);
                        })
                  }
              })
          }
      },
    }
</script>

<style scoped>
</style>


