<template>
  <div class="all" style="text-align: left;height: 100%;width: 10%" >
  <el-menu
  :default-active="$route.path"
  class="el-menu-vertical-demo"
  background-color="#f7f7f7"
  router>
    <el-menu-item :index="'/craftTable/'+uid">
        <template slot="title">
            <i class="el-icon-box"></i>
            <span>工作台</span>
        </template>
    </el-menu-item>
    <el-menu-item :index="'/myDocs/'+uid">
        <template slot="title">
          <i class="el-icon-folder"></i>
          <span>我的文档</span>
        </template>
    </el-menu-item>
    <el-submenu index="1">
        <template slot="title" >
              <i class="el-icon-office-building" ></i>
              <span >我的团队 &nbsp;&nbsp;&nbsp;&nbsp;</span>
        </template>
        <el-menu-item>
          <span>新建团队</span>
          <i class="el-icon-circle-plus-outline" @click.stop="digitalFromVisible=true" ></i>
        </el-menu-item>
        <el-menu-item v-for="(team,index) in this.team" :key="index" :index="'/myTeam/'+uid+'/'+team.TID">
          <div>
            <span >-</span>
            <span >{{ team.teamName.length>7?team.teamName.substring(0,7)+'...':team.teamName }}</span>
          </div>
        </el-menu-item>
    </el-submenu>
    <el-menu-item :index="'/personalRecycleBin/'+uid">
        <i class="el-icon-milk-tea"></i>
        <span>回收站</span>
    </el-menu-item>
  </el-menu>
  <el-dialog title="新建团队空间" :visible.sync="digitalFromVisible">
    <el-form :model="form" :rules="rules" ref="forms">
      <el-form-item label="空间名称" label-width="20%" prop="teamName">
        <el-input v-model="form.teamName" auto-complete="false" :validate-event="true"></el-input>
      </el-form-item>
    </el-form >
    <div slot="footer" class="dialog-footer">
      <el-button @click="digitalFromVisible = false">取 消</el-button>
      <el-button type="primary" @click="FormSubmit">确 定</el-button>
    </div>
  </el-dialog>
  </div>
</template>

<script>
  import QS from "qs";
  export default {
    name: "LeftSliderPro",
    //需要传入当前用户的团队和当前用户的id
    props:["team"],
    data(){
      return{
        first1:"工作台",
        second1:"我的文档",
        third1:"我的团队",
        forth1:"回收站",
        isFolded:true,
        uid:this.$store.state.userProfile.UID,
        digitalFromVisible:false,
        form: {
          teamName: '',
          teamOwner:this.$store.state.userProfile.UID
        },
        rules:{
          teamName:[
            {
              validator:this.validateTeamName,
              trigger: 'blur'
            },
            {
              required:true,
              message:"请填写团队名字",
              trigger:'blur'
            }
          ]
        }
      }
    },
    methods:{
      change(){
        this.isFolded = !this.isFolded;
      },
      go1() {
        this.$router.push("/craftTable/" + this.$store.state.userProfile.UID);
      },
      go2(){
        this.$router.push("/myDocs/"+this.$store.state.userProfile.UID);
      },
      go3(TID){
        this.$router.push("/myTeam/"+this.$store.state.userProfile.UID+"/"+TID);
      },
      go4(){
        this.$router.push("/personalRecycleBin/"+this.$store.state.userProfile.UID);
      },
      onInput(){
        this.$forceUpdate();
      },
      validateTeamName(rule,value,callback){
        console.log("111");
        this.$axios
          .post("/api/checkTeamName/",QS.stringify(this.form))
          .then(response =>{
            if(this.form.teamName!==""&&response.data.isExist===false){
              callback(new Error("团队名称重复!"));
            }else{
              callback();
            }
          }).catch( error =>{
          this.$message({
            message:"该页面出现了一些情况",
            type:"error",
            customClass:"c-msg",
            duration:0,
            showClose:true
          });
          Promise.reject(error);
        });
      },
      FormSubmit(){
        this.$refs.forms.validate(valid => {
          if(valid){
            this.digitalFromVisible=false;
            let newForm =new FormData();
            newForm.append("teamOwner",this.$store.state.userProfile.UID);
            newForm.append("teamName",this.form.teamName);
            this.$axios
              .post("/api/checkTeam/",newForm)
              .then(response => {
                if(response.data.status){
                  this.form.teamName="",
                    this.team.push({teamName:response.data.teamName,TID:response.data.TID}) ;
                  this.$emit("changeTeam",{teamName:response.data.teamName,TID:response.data.TID});
                  this.$message({
                    message:"创建成功",
                    type:"success",
                    customClass:"c-msg"
                  })
                 ;
                }
                else{
                  this.form.teamName="",
                    this.$message({
                      message:"创建失败",
                      type:"error",
                      customClass:"c-msg"
                    })
                }
              })
              .catch(error =>{
                this.$message({
                  message:"该页面出现了一些情况",
                  type:"error",
                  customClass:"c-msg",
                  duration:0,
                  showClose:true
                });
                Promise.reject(error);
              })
          }
        })
      }
    },
  }
</script>

<style>
.el-submenu .el-menu-item{
    min-width:191px !important;
}
</style>
<style lang="scss">
</style>



