<template>
	<div style="text-align:left;height:70px;border-bottom:1px solid #e8e8e8;vertical-align:middle">
		<!--logo-->
		<div style="margin-left:2%;height:100%;width:20%;display:inline-block;vertical-align:middle;">
			<p style="display:inline-block;height:100%;vertical-align:middle;margin:auto"></p>
			<img @click="toHome()" style="width:14%;vertical-align:middle;cursor:pointer" src="/static/logo.png"/>
			<h1 @click="toHome()" style="margin:auto;display:inline-block;vertical-align:middle;cursor:pointer">金刚石文档</h1>
		</div>
    	<!--搜索框-->
		<div style="
				display:inline-block;
				width:15%;
				margin-left:50%;
				text-align:right;
				"
			v-if="this.showSearch"
		>
			<el-input v-model="target" :placeholder="'搜索文档'" style="width:75%;display:inline-block"></el-input>
			<el-button plain icon="el-icon-search" style="width:20%;display:inline-block" @click="emitSearch()"></el-button>
		</div>
		<!--占位-->
		<div v-else style="display:inline-block;width:15%;margin-left:50%"></div>
		<!--头像，消息提醒-->
		<div
			style="
				display:inline-block;
				vertrical-align:middle;
				width:10%;
				text-align:right;
				margin-left:5px;
				cursor:default;
				font-size:200%
			"
		v-if="this.showAvatar"
		>
		<!--点击消息弹出的提示框-->
		<el-popover
			ref="message_pop"
			popper-class="messageBox"
			@after-leave="readAllMessage()"
		>
			<div style="width:100%;margin-left:0px;margin-right:0px;padding-left:0px">
				<h3 style="margin-top:0px;margin-bottom:0px;padding-top:1ex;padding-bottom:1ex;padding-left:1ex">我的消息</h3>
				<div v-if="messages.length!=0" id="messageSector" style="border-top:1px solid #e8e8e8;width:100%;list-style-type:none;padding-left:0px">
					<div v-for="(message,i) in messages" :key="i" class="messageItem">
						<!--消息项-->
						<!--消息编号，顺便标记是否已读-->
						<p style="margin-bottom:0px;margin-top:0px;margin-right:1ex;margin-left:1ex">
							<el-badge :is-dot="!message.Read" :type="message.Type=='Invitation'?'warning':'danger'">
							<span style="font-weight:bold">{{i+1}}.</span>
							</el-badge>
							<span>&nbsp;&nbsp;{{message.Content}}</span>
						</p>
						<!--显示时间-->
						<p style="text-align:right;margin-top:0px;margin-bottom:0px;margin-right:1ex">
							<span class="clear-one" @click="removeOne(message.MessageID,i)">删除</span>
							<span><i class="el-icon-alarm-clock"></i>{{message.Time}}</span>
						</p>
						<!--如果是团队邀请，那么就有两个链接-->
						<div v-if="message.Type=='Invitation'" style="text-align:right;margin-right:1ex">
							<span class="myLink" @click="replyInvitation(message.MessageID,true)">同意</span>
							<span class="myLink" @click="replyInvitation(message.MessageID,false)">拒绝</span>
						</div>
					</div>
				</div>
				<div v-else style="text-align:center;width:100%;padding:1ex;border-top:1px solid #e8e8e8">
					<p style="font-weight:bold;width:100%">当前没有任何消息</p>
				</div>
				<!--消息列表的底栏-->
				<div v-if="messages.length!=0" id="messageFooter" style="text-align:right;padding:0.5ex;border-top:1px solid #e8e8e8">
					<span class="clear-all" @click="removeAll()"><i class="el-icon-delete"></i>&nbsp;全部清空</span>
				</div>
			</div>
		</el-popover>
		<p style="display:inline-block;height:100%;vertical-align:middle;margin:auto;width:0%"></p>
		<!--消息标志-->
		<el-badge :type="NumUnRead!=0?'danger':'info'" :value="NumUnRead==0?messages.length:NumUnRead" :max="99" :hidden="NumUnRead<=0&&messages.length<=0">
			<i
			:class="NumUnRead<=0?'el-icon-bell':'el-icon-message-solid'"
			v-popover="'message_pop'"
			style="cursor:pointer"
			>
			</i>
		</el-badge>

		<!--点击头像弹出的提示框-->
		<el-popover
			ref="icon_pop"
			style="padding:0px 0px 0px 0px"
			popper-class="avatarBox"
		>
			<h3 style="margin:auto auto auto auto;padding:0.3ex;border-bottom:1px solid #e8e8e8">&nbsp;{{name}}</h3>
			<!--个人主页-->
			<div class="option" style="margin-top:0px;font-size:125%;cursor:pointer">
				<div style="padding-top:4px;padding-bottom:4px">
					&nbsp;<i class="el-icon-user-solid" style="margin-top:2px;margin-bottom:2px"></i>
					<span style="text-align:center" @click="jumpToPerson()">&nbsp;个人主页</span>
				</div>
			</div>
			<!--注销登录-->
			<div class="option" style="margin-top:0px;padding:2px auto 2px auto;font-size:125%;cursor:pointer">
				<div style="padding-top:4px;padding-bottom:4px" @click="logout()">
					&nbsp;<i class="el-icon-s-release" style="display:inline-block"></i>
					<span style="text-align:center">&nbsp;注销登录</span>
				</div>
			</div>
		</el-popover>
      <p style="display:inline-block;height:100%;vertical-align:middle;margin:auto;width:0%"></p>
      <el-avatar v-if="imageUrl" :src="imageUrl" v-popover:icon_pop style="vertical-align:middle;cursor:pointer;display:inline-block;"></el-avatar>
      <el-avatar v-else v-popover:icon_pop icon="el-icon-user-solid" style="vertical-align:middle;cursor:pointer;display:inline-block;"></el-avatar>
    </div>
  </div>
</template>
<script>
import QS from 'qs';
export default {
    props:{
		showAvatar:Boolean,//是否展示头像，要么是user要么是visitor
		showSearch:Boolean//是否展示搜索框
    },
    data(){
		return{
			name:this.$store.state.userProfile.name,
			imageUrl:this.$store.state.userProfile.avatarUrl,
			target:"",//搜索框内的内容
			NumUnRead:0,
			messages:[]
		}
    },
    mounted(){
		console.log(this.imageUrl);
		console.log(this.$store.state.userProfile.avatarUrl);
		//this.messages.sort(this.sortMessage);
		this.getMessages();
    },
    methods:{
		/*返回首页 */
		toHome(){
			this.$router.push({
				path:"/"
			})
		},
		/*消息排序函数 */
		/**
		 * 邀请在最前面，按照时间由近及远
		 * 未读消息靠前，按照时间由近及远
		 * 已读消息在后，按照时间由近及远
		 */
		sortMessage(a,b){
			if(a.Type=='Invitation'&&b.Type=='Invitation'){//都是邀请信息，近的在前
				return a.Time>b.Time?-1:1;
			}
			else if(a.Type=='Normal'&&b.Type=="Normal"){//二者都是普通信息
			if((a.Read&&b.Read)||(a.Read&&b.Read)){//都读过或者都没读过
				return a.Time>b.Time?-1:1;
			}
			else{
				return a.Read?1:-1;
			}
			}
			else{//一个是邀请，另一个不是，邀请在前
				return a.Type=='Invitation'?-1:1;
			}
		},
      	/*点击注销的时候执行的函数 */
		logout(){
			this.$store.commit('changeUserState',false);
			this.$store.commit('changeUID',"");
			this.$store.commit('changeAvatarUrl',"");
			this.$store.commit('changeUserName',"");
			this.$message({
				message:"注销成功，欢迎再来",
				type:"success",
				duration:1000,
				showClose:true
			})
			this.$router.push({
				path:"/",
			});
		},
		/*跳转到个人主页 */
		jumpToPerson(){
			this.$router.push({
				path:"/PersonIndex"
			})
		},
		emitSearch(){
			console.log("发送的内容："+this.target);
			this.$emit("search",this.target);
		},
      	/*从后端获取消息 */
		getMessages(){
			let form = new FormData();
			form.append("UID",this.$store.state.userProfile.UID);
			this.$axios.post("/api/myMessage/",form)
			.then(response=>{
				this.NumUnRead=response.data.UnRead;
				this.messages=response.data.messages;
				this.messages.sort(this.sortMessage);
			})
			.catch(error=>{
				this.$message({
					message:"myMessage好像出了点问题",
					type:"error",
					duration:1000,
					showClose:true
				})
			})
		},
        /*标记消息为已读 */
        readAllMessage(){
            let form = {};
            let m = [];
            this.messages.forEach(item=>{
				m.push({
					MessageID:item.MessageID,
					Type:item.Type
				});
				this.$set(item,'Read',true);
			})
			this.NumUnRead=0;
			console.log("messages:"+this.messages);
            form = {Messages:m};
            console.log(m);
            this.$axios.post("/api/readMessage/",form)
            .then(response=>{
            })
            .catch(error=>{
              this.$message({
                message:"/api/readMessage/好像出了点问题",
                type:"error",
                duration:1000,
                showClose:true
              })
            })
      	},
      	/*团队邀请操作 */
		replyInvitation(mid,opr){
			let form = new FormData();
			form.append("MID",mid);
			form.append("Opr",opr);
			console.log("MID:"+mid);
			console.log("Opr:",opr);
			this.$axios.post("/api/replyInvitation/",form)
			.then(response=>{
				if(response.data.Status){
				if(opr){//加入成功
					this.$message({
					message:"您已成功加入"+response.data.Name,
					type:"success",
					duration:1000,
					showClose:true
					})
				}
				}
			})
			.catch(error=>{
				this.$message({
					message:"replyInvitation好像有点问题",
					type:"error",
					duration:1000,
					showClose:true
				})
			})
		},
		/*根据mid删除一条消息 */
		removeOne(mid,index){
			let form = {};
			let mids = []
			mids.push(mid);
			form={
				MID:mids,
				UID:this.$store.state.userProfile.UID
			}
			this.$axios.post("/api/deleteMessage/",form)
			.then(response=>{
				if(!response.data.Status){
					this.$message({
						message:"服务器忙，请待会再试",
						type:"error",
						duration:1000,
						showClose:true
					})
				}
				else{
					console.log(this.messages[index]);
					this.messages.splice(index,1);
				}
			})
			.catch(error=>{
				this.$message({
					message:"/api/deleteMessage好像有点问题",
					type:"error",
					duration:1000,
					showClose:true
				})
			})
		},
		/*删除当前的全部消息 */
		removeAll(){
			let m = [];
			for(var i=0;i<this.messages.length;i++){
				if(this.messages[i].Type!='Invitation'){
					m.push(this.messages[i].MessageID);
				}
			}
			let form = {};
			form={
				UID:this.$store.state.userProfile.UID,
				MID:m
			}
			this.$axios.post("/api/deleteMessage/",form)
			.then(response=>{
				if(!response.data.Status){
					this.$message({
						message:"服务器忙，请稍后再试",
						type:"error",
						duration:1000,
						showClose:true
					})
				}
				else{
					this.messages=[];
				}
			})
			.catch(error=>{
				this.$message({
					message:"/api/deleteMessage好像有点问题",
					type:"error",
					duration:1000,
					showClose:true
				})
			})
		}
    }
}
</script>
<style lang="scss">
.messageBox{
	padding:0px 0px 0px 0px;
	background: white;
	width:30%;
	border:1px solid #e8e8e8;
}
.avatarBox{
	padding:0px 0px 0px 0px;
	border:1px solid #e8e8e8;
}
</style>
<style scoped>
  .option:hover{
    background:#f0f0f0;
    font-weight:bold
  }
  .option:active{
    background:#e8e8e8;
    font-weight:bold;
  }
  .messageItem{
    width:100%;
    font-weight:normal;
    font-size:100%;
    cursor:default;
    margin-left: 0px;
    padding-top:1ex;
    padding-bottom:1ex;
    word-break:break-all;
    word-wrap: break-word;
  }
  .messageItem:hover{
    font-weight:normal;
    background:#e8e8e8;
    font-size:100%;
  }
  #messageSector{
	  overflow-y:auto;
	  overflow-x: hidden;
	  max-height: 40ex;
  }
  .myLink{
    cursor: pointer;
    color:blue;
  }
  .myLink:hover{
    color:red;
    font-weight: bold;
    text-decoration: underline;
  }
  .clear-all{
	  font-weight:normal;
	  font-size:120%;
	  cursor: pointer;
  }
  .clear-all:hover{
	  font-weight:bold;
	  color:blue;
	  cursor: pointer;
  }
  .clear-one{
	  font-weight:normal;
	  font-size:100%;
	  cursor: pointer;
  }
  .clear-one:hover{
	  font-weight:bold;
	  color:blue;
	  cursor: pointer;
  }
</style>



