<template>
  <div>
    <div>
      <my-header :show-avatar="true" :show-search="true" @search="ToSearch1"></my-header>
    </div>
    <div>
      <div id="left" style="display: inline-block;width: 10%;vertical-align: top;padding: 0px 30px 0px 0px;">
        <!--  <LeftSider :team="myTeam" style="width: 100%" @changTeam="changeTeam"></LeftSider>-->
        <LeftSliderPro :team="myTeam" style="width: 100%;float: left" @changTeam="changeTeam"></LeftSliderPro>
      </div>
      <div id="main" style="display: inline-block;width: 70%;vertical-align: top;text-align: left">
        <div v-if="Documents11.length>0">
          <ShowPersonFile :documents1="Documents11"></ShowPersonFile>
        </div>
      </div>
      <div id="right" style="display: inline-block;width: 15%;vertical-align: center">
          <RightSlider :tagSelectionVisible="true" :showUpload="true" :documents="Documents2" :documents1="Documents1" style="width: 100% ;margin-left: 10%" @searchTag="ToSearch2"></RightSlider>
      </div>
    </div>
  </div>
</template>

<script>
  import myHeader from "../../components/myHeader/myHeader";
  import LeftSider from "../../components/LeftSider/LeftSider";
  import ShowPersonFile from "../../components/ShowPersonalFile/ShowPersonFile";
  import RightSlider from "../../components/RightSlider/RightSlider.vue";
  import LeftSliderPro from "../../components/LeftSliderPro/LeftSliderPro";
  import QS from "qs";
  export default {
    name: "myDocs",
    components:{
      myHeader,
      LeftSider,
      ShowPersonFile,
      RightSlider,
      LeftSliderPro
    },
    created:function(){
      var uid = this.$store.state.userProfile.UID;
      var param_data = {UID :uid};
      this.$axios
        .post("/api/someInfo1/",QS.stringify(param_data))
        .then(response => {
          this.myTeam = response.data.myTeam;
          this.Documents1 = response.data.Documents1;
          this.Documents11 = response.data.Documents1;
          this.Documents2 = response.data.Documents2;
          this.Documents11Copy = this.Documents11;
          console.log("22222");
          console.log(this.Documents2);
        }).catch(error =>{
        alert(error);
      })
    },
    data(){
      return{
        myTeam:[],
        Documents1:[
        ],
        Documents11:[
        ],
        Documents11Copy:[],
        Documents2:[
        ],
        Documents22:[],
        Documents3:[
        ],
        Documents33:[],
        Documents4:[]
      }
    },
    methods:{
      Select(target){
        if(this.cur_show=='Edit'){
          this.Documents22=this.Documents2;
        }
        else if(this.cur_show=='lookUp'){
          this.Documents11=this.Documents1;
        }
        else if(this.cur_show=='Collect'){
          this.Documents33=this.Documents3;
        }
        if(target=='lookUp'){
          this.cur_show=target;
        }
        else if(target=="Edit"){
          this.cur_show=target;
        }
        else if(target=="Collect"){
          this.cur_show=target;
        }
        this.search="";
        console.log(target);
        let selectedTag = document.getElementsByClassName("selected");
        if(selectedTag!=null){
          var t;
          for(t=0;t<selectedTag.length;t++){
            selectedTag[t].setAttribute("class","option");
          }
        }
        /*把当前变成selected */
        document.getElementById(target).setAttribute("class","selected");
      },
      changeTeam(params){
        this.myTeam.push(params);
      },
      ToSearch1(target){
        console.log("王勇".toLowerCase());
        let newArr = [];
        let search1 = target.toLowerCase();
        console.log(search1);
        var i;
        for(i=0;i<this.Documents1.length;i++){
          if(this.Documents1[i].documentName.length<search1.length){
            let newArr1 = this.Documents1[i].documentName.toLowerCase();
            console.log(newArr1);
            if(search1.indexOf(newArr1)!=-1){
              newArr.push(this.Documents1[i]);
            }
          }
          else{
            let newArr1 = this.Documents1[i].documentName.toLowerCase();
            console.log(newArr1);
            if(newArr1.indexOf(search1)!=-1){
              newArr.push(this.Documents1[i]);
            }
          }
        }
        console.log("查询数组");
        this.Documents11=newArr;
      },
      ToSearch2(target){
        if(target==""){
          this.Documents11=this.Documents11Copy;
          return;
        }
        console.log("查询数组");
        console.log(target);
        let newArr = [];
        let search1 = target.toLowerCase();
        console.log(search1);
        var i;
        var j;
        for(i=0;i<this.Documents1.length;i++){
            for(j=0;j<this.Documents1[i].Tags.length;j++){
                  if(search1.localeCompare(this.Documents1[i].Tags[j].name)==0){
                      newArr.push(this.Documents1[i]);
                      break;
                  }
            }
        }
        this.Documents11=newArr;
        console.log(this.Documents11);
      }
    },
  }
</script>


<style scoped>
  .option{
    font-size:150%;
    font-weight: normal;
  }
  .option:hover{
    font-size:100%;
    font-weight: bold;
    border-bottom:2px solid darkgray;
  }
  .option:active{
    font-size: 100%;
    font-weight: bold;
    border-bottom: 2px solid black;
  }
  .selected{
    font-size: 50%;
    font-weight: bold;
    border-bottom: 2px solid gray;
  }
</style>



