<template>
    <div style="padding:1ex">
		<h2>{{name}}</h2>
		<hr style="width:100%">
		<p style="text-align:left;padding-left:1ex"><i class="el-icon-info"></i>&nbsp;团队描述：<br></p>
		<p 
			class="hasDes" 
			v-if="this.describe!=null&&showDes"
			@mouseover="handleHover" 
		>
			{{this.describe}}
		</p>
		<p 
			class="hasntDes" 
			v-if="this.describe==null&&showDes" 
			@mouseover="handleHover"
		>
			{{defaultDes}}
		</p>
		<div v-if="!showDes" @mouseout="submitChange()" style="padding:0px 1ex 1ex 1ex">
			<el-input type="textarea" :autofocus="true" v-model="newDescription" :placeholder="this.describe==null?'请输入新的团队描述':describe"></el-input>
		</div>
    </div> 
</template>
<script>
const clickoutside = {
    update() {},
};
export default {
    props:{
		name: String,
		describe:String,
		tid:Number,//这个团队的id
		level:Number//当前用户等级
    },
	name:'TeamDescribe',
    data(){
        return{
            form: {
              describe:this.describe,
              name: this.name
			},
			originDes:this.describe,//原本的描述，用来在后面判断是否有变化
			defaultDes:"这个团队没有描述，在此添加描述",
			newDescription:this.describe,//新的团队描述
			showDes:true//是否展示团队描述，还是输入框
        }
    },
    methods: {
		handleHover(){
			console.log("团队描述：当前等级是"+this.level);
			if(this.level>=5)this.showDes=false;
		},
		submitChange(){
			this.showDes=true;
			if(this.newDescription==''||this.newDescription==this.originDes){
				console.log('没有新描述');
				return;
			}
			let form = new FormData();
			form.append("UID",this.$store.state.userProfile.UID);
			form.append("TID",this.tid);
			form.append("Words",this.newDescription);
			this.$axios.post("/api/changeTeamDescription/",form)
			.then(response=>{
				if(response.data.Status){
					this.$message({
						message:"团队描述修改成功",
						type:"success",
						duration:1000,
						showClose:true
					});
					this.describe=this.newDescription;
				}
				else{
					this.$message({
						message:"服务器忙，请稍后",
						type:"error",
						duration:1000,
						showClose:true
					});
				}
			})
			.catch(error=>{
				this.$message({
					message:"changeTeamDescription好像有点问题",
					type:"error",
					duration:1000,
					showClose:true
				});
			})
		}
    }    
}
</script>
<style scoped>
.hasntDes{
	color:grey;
	text-align:left;
	padding:0px 1ex 1ex 1ex;
}
.hasDes{
	color:black;
	text-align:left;
	padding:0px 1ex 1ex 1ex;
}
</style>



