<template>
  <div>
    <div>
      <my-header :showAvatar="true" :showSearch="true" @search="ToSearch"></my-header>
    </div>
    <div>
      <div id="left" style="display: inline-block;width: 10%;vertical-align: top;border-right: 1px solid #DCDCDC;text-align:left">
        <teamLeft :documents="Documents1" :team="team" style="width: 100%"></teamLeft>
      </div>
      <div id="main" style="display: inline-block;width: 70%;vertical-align: top;text-align: left">
        <div v-if="this.Documents11.length>0">
          <ShowPersonFile :documents1="Documents11"></ShowPersonFile>
        </div>
      </div>
      <div id="right" style="display:inline-block;width:15%;vertical-align:top;text-align:center">
        <!--<div>
          <el-row>
            <el-col :span="24">
              <el-input v-model="search" @keyup.enter.native="ToSearch" style="width: 100%" >
                <el-button slot="append" icon="el-icon-search" id="search" @click="ToSearch" ></el-button>
              </el-input>
            </el-col>
          </el-row>Person
        </div>
        下面rightsliderteam传入的参数是否需要TID
        -->
        <RightSliderTeam :tagSelectionVisible="true" :showUpload="true" :documents1="Documents1" :documents="Documents4" :TID="tid" style="width:100%;" @searchTag="ToSearch2"></RightSliderTeam>
        <TeamDescribe :name="team.name" :level="mylevel" :describe="team.describe" class="descriptor" :tid="parseInt(tid)"></TeamDescribe>
        <memberList class="memberList" :tid="tid1" :my-level="mylevel"></memberList>
      </div>
    </div>
  </div>
</template>

<script>
    import myHeader from "../../components/myHeader/myHeader";
    import teamLeft from "../../components/teamLeft/teamLeft";
    import ShowPersonFile from "../../components/ShowPersonalFile/ShowPersonFile";
    import RightSliderTeam from "../../components/RightSliderTeam";
    import TeamDescribe from "../../components/TeamDescribe/TeamDescribe";
    import memberList from "../../components/memberList/memberList";
    import QS from "qs";
    export default {
        name: "myTeam",
        components:{
            myHeader,
            teamLeft,
            ShowPersonFile,
            RightSliderTeam,
            TeamDescribe,
            memberList
        },
        created:function(){
          console.log(this.tid);
          var uid = this.$store.state.userProfile.UID;//这里应该是要有UID、TID
          var param_data = {UID :uid, TID:this.tid};
          this.$axios
          .post("/api/TeamInfo/",QS.stringify(param_data))
          .then(response => {
              this.team = response.data.team;
              this.mylevel=response.data.mylevel;
              console.log("团队页面："+this.mylevel);
              this.Documents1 = response.data.Documents1;
              this.Documents11 = response.data.Documents1;
              this.Documents11Copy = this.Documents11;
              this.Documents4 = response.data.Documents4;//团队模板
              console.log("22222");
          }).catch(error =>{
            alert(error);
          })
        },
      data(){
          return{
            mylevel:0,
            show:true,
            search:"",
            team:{TID:this.tid,name:"",describe:""},
            Documents1:[],
            Documents11:[],
            Documents4:[],
            Documents11Copy:[],
            tid:this.$route.params.TID,
            tid1:parseInt(this.$route.params.TID)
          }
        },
        methods:{
          showWhat(show1){
            console.log("这个函数执行了嘛");
            console.log("show1="+show1);

            this.show=show1;
          },
          ToSearch(target){
            console.log("王勇".toLowerCase());
            let newArr = [];
            let search1 = target.toLowerCase();
            console.log(search1);
            var i;
                for(i=0;i<this.Documents1.length;i++){
                    if(this.Documents1[i].documentName.length<search1.length){
                        let newArr1 = this.Documents1[i].documentName.toLowerCase();
                        console.log(newArr1);
                        if(search1.indexOf(newArr1)!=-1){
                            newArr.push(this.Documents1[i]);
                        }
                    }
                    else{
                      let newArr1 = this.Documents1[i].documentName.toLowerCase();
                      console.log(newArr1);
                      if(newArr1.indexOf(search1)!=-1){
                          newArr.push(this.Documents1[i]);
                      }
                    }
                }
                console.log("查询数组");
                this.Documents11=newArr;
          },
          ToSearch2(target){
            if(target==""){
              this.Documents11=this.Documents11Copy;
              return;
            }
            console.log("查询数组");
            console.log(target);
            let newArr = [];
            let search1 = target.toLowerCase();
            console.log(search1);
            var i;
            var j;
            for(i=0;i<this.Documents1.length;i++){
              for(j=0;j<this.Documents1[i].Tags.length;j++){
                if(search1.localeCompare(this.Documents1[i].Tags[j].name)==0){
                  newArr.push(this.Documents1[i]);
                  break;
                }
              }
            }
            this.Documents11=newArr;
            console.log(this.Documents11);
          }
        },
    }
</script>


<style scoped>
  .option{
    font-size:150%;
    font-weight: normal;
  }
  .option:hover{
    font-size:100%;
    font-weight: bold;
    border-bottom:2px solid darkgray;
  }
  .option:active{
    font-size: 100%;
    font-weight: bold;
    border-bottom: 2px solid black;
  }
  .selected{
    font-size: 50%;
    font-weight: bold;
    border-bottom: 2px solid gray;
  }
  .descriptor{
    background:white;
    border:1px solid #e8e8e8;
    border-radius:1ex;
    margin-top:2ex;
  }
  .memberList{
    background:white;
    border:1px solid #e8e8e8;
    border-radius:1ex;
    margin-top:2ex
  }
</style>



